.sort {
  display: flex;
  position: relative;

  @include vp-767 {
    margin-top: 10px;
  }
}

.sort__wrapper {
  &--by-rating {
    margin-right: 10px;
  }
}

.sort__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 61px;
  height: 30px;
  background-color: #282B34;
  border: none;
  border-radius: 6px;
  padding: 0;
  cursor: pointer;

  &:hover,
  &.active {
    background-color: #3E4046;
  }

  @include vp-1023 {
    border: 1px solid #67676B;
  }
}

.sort__body {
  display: none;
}

.sort__body.active {
  display: block;
  position: absolute;
  top: 26px;
  left: -39px;
  min-width: 171px;
  background-color: #3E4046;
  border-radius: 6px 0px 6px 6px;
  z-index: 10;

  @include vp-1023 {
    min-width: 352px;
    border-radius: 10px;
    padding: 11px;
    top: 34px;
    left: -115px;
  }
}

.sort__close-btn {
  position: absolute;
  display: block;
  top: 0px;
  right: 2px;
  background-color: transparent;
  border: none;
  width: 30px;
  height: 30px;
  cursor: pointer;

  &:hover {
    opacity: 0.6;
  }

  @include vp-767 {
    top: 8px;
    right: 8px;
  }
}

.sort__item {

  span {
    @include font-settings ($ff-inter, 600, 14px, 1);
    display: block;
    padding: 8px 8px 4px;

    @include vp-1023 {
      padding: 13px 32px 9px;
    }
  }
}

.sort__link {
  @include font-settings ($ff-inter, 400, 14px, 1);
  display: block;
  padding: 8px 8px 4px;

  @include vp-1023 {
    padding: 13px 32px 9px;
  }

  &:hover,
  &:active {
    background-color: #353838;
  }

  &.active {
    background-color: #2F3239;
  }
}
