.events {
  background: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 53px 40px 73px;

  @include vp-1023 {
    padding: 20px 14px;
    // margin-top: 80px;
  }
}

.events__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 68px;

  h2 {
    @include font-settings ($ff-inter, 700, 35px, 1);
    letter-spacing: 0.9px;
    text-transform: uppercase;
  }

  @include vp-1023 {
    flex-direction: column;
  }
}

.events__add-btn {
  @include font-settings ($ff-inter, 500, 14px, 1);
  min-width: 190px;
  background-color: $color-accent;
  border: none;
  border-radius: 10px;
  color: $color-default-black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 20px;
  cursor: pointer;
  margin-right: 4px;

  svg {
    fill: $color-default-black;
    margin-right: 13px;
  }

  &:hover {
    background-color: $color-accent-hover;
  }

  &:active {
    opacity: 0.6;
  }

  @include vp-1023 {
    order: -1;
    // margin-top: -80px;
    margin-bottom: 50px;
    min-width: 290px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.events__content {
  display: grid;
  grid-template-columns: auto 360px;
  column-gap: 40px;
  row-gap: 15px;

  @include vp-1279 {
    grid-template-columns: auto 295px;
  }

  @include vp-1023 {
    grid-template-columns: auto;
  }
}

.events__list {
  display: flex;
  flex-wrap: wrap;
  gap: 50px 30px;
  margin-bottom: 20px;

  @include vp-1023 {
    justify-content: center;
  }
}

.events__item {
  position: relative;
  width: 100%;
  max-width: 361px;
  background-color: #262931;
  border-radius: 0px 0px 20px 20px;

  &:hover {
    box-shadow: 0px 0px 20px #262931;
  }

  &:active {
    opacity: 0.6;
  }

  @include vp-1279 {
    max-width: 289px;
  }
}

.events__item a {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.events__date {
  position: absolute;
  width: 82px;
  height: 82px;
  background-color: #FFC600;
  border-radius: 10px;
  top: -33px;
  left: 29px;
  padding: 16px 10px 14px;
  z-index: 2;

  p {
    @include font-settings ($ff-inter, 600, 31px, 1);
    text-align: center;
    letter-spacing: 0.96px;
    color: #14161B;
    margin-bottom: 4px;
  }

  span {
    @include font-settings ($ff-inter, 600, 15px, 1);
    display: block;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.96px;
    color: #14161B;
  }

  @include vp-1279 {
    width: 62px;
    height: 62px;

    p {
      font-size: 24px;
    }

    span {
      font-size: 12px;
    }
  }
}

.events__image {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 185px;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include vp-1279 {
    height: 135px;
  }
}

.events__expired {
  position: absolute;
  top: 26px;
  right: 23px;
  min-width: 105px;
  height: 24px;
  background: rgba(26, 29, 37, 0.4);
  backdrop-filter: blur(5px);
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;

  svg {
    margin-right: 7px;
  }

  p {
    @include font-settings ($ff-inter, 600, 10px, 1.6);
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: #D1D2D3;
  }
}

.events__tags-list {
  display: flex;
  flex-wrap: wrap;
}

.events__tags-item {
  margin: 0 13px 13px 0;
  border: 1px solid #FFFFFF;
  border-radius: 20px;
  opacity: 0.5;
  padding: 0 15px 2px;

  span {
    @include font-settings ($ff-inter, 400, 14px, 1);
    letter-spacing: 0.3px;
  }
}

.events__text {
  padding: 29px 20px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;

  h3 {
    @include font-settings ($ff-inter, 600, 17px, 1.56);
    letter-spacing: 0.5px;
    text-transform: uppercase;
    margin-bottom: 18px;
    flex-grow: 1;
  }

  p {
    @include font-settings ($ff-inter, 500, 14px, 1);
    color: $color-default-black;
    letter-spacing: 0.96px;
    text-transform: uppercase;
    background-color: $color-accent;
    width: fit-content;
    border: none;
    border-radius: 10px;
    padding: 12px 20px;
    cursor: pointer;

    &:hover {
      background-color: $color-accent-hover;
    }
  }
}

.events__show-more-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  min-height: 47px;
  background-color: #262931;
  border: none;
  border-radius: 10px;
  color: $color-default-white;
  cursor: pointer;

  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
  }

  svg {
    margin-right: 17px;
  }

  &:hover {
    background-color: #76777C;
    color: $color-default-white;
  }

  &:active {
    opacity: 0.5;
  }
}
