.updates-comments {
  h3 {
    @include font-settings ($ff-inter, 500, 22px, 1.58);
    letter-spacing: 0.96px;
    text-transform: uppercase;
    margin-bottom: 16px;

    @include vp-1023 {
      font-size: 18px;
      line-height: 1.93;
      margin-bottom: 11px;
    }

    span,
    b {
      @include font-settings ($ff-inter, 500, 22px, 1.58);
      color: #A3A5A8;

      @include vp-1023 {
        font-size: 18px;
        line-height: 1.93;
      }
    }
  }
}

.updates-comments__list {
  display: flex;
  flex-direction: column;
}

.updates-comments__item {
  position: relative;
  max-width: 730px;
  background-color: #262931;
  border-radius: 8px;
  padding: 24px;
  display: flex;
  margin-bottom: 15px;

  @include vp-767 {
    flex-direction: column;
    padding: 20px 20px 18px;
    margin-bottom: 20px;
  }

  &--secondary {
    max-width: 642px;
    margin-left: 90px;

    @include vp-767 {
      margin-left: 0;
    }
  }
}

.updates-comments__likes {
  min-width: 40px;
  max-height: 100px;
  margin-right: 24px;
  background-color: #1D2028;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  @include vp-767 {
    max-width: 100px;
    height: 40px;
    flex-direction: row;
    align-self: flex-end;
    order: 1;
    margin-right: 0;
  }
}

.updates__likes-count {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.updates__btn {
  @include font-settings ($ff-inter, 500, 20px, 1);
  width: 40px;
  height: 34px;
  background-color: transparent;
  border: none;
  color: $color-default-white;
  cursor: pointer;

  @include vp-767 {
    height: 40px;
  }

  &--like {
    border-radius: 10px 10px 0 0;

    @include vp-767 {
      border-radius: 10px 0 0 10px;
    }
  }

  &--dislike {
    border-radius: 0 0 10px 10px;

    @include vp-767 {
      border-radius: 0 10px 10px 0;
    }
  }

  &:hover,
  &:active {
    background-color: #14161B;
  }

  span {
    @include font-settings ($ff-inter, 500, 16px, 1);
  }
}

.updates-comments__heading {
  display: flex;
  align-items: center;
  margin-bottom: 15px;

  @include vp-767 {
    justify-content: space-between;
  }
}

.updates-comments__author {
  display: flex;
  align-items: center;
}

.updates-comments__author-img {
  position: relative;
  width: 32px;
  height: 32px;
  background-color: #1A1D25;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 10px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.updates-comments__author-link {
  @include font-settings ($ff-inter, 500, 16px, 1);
  margin-right: 18px;
}

.updates-comments__time {
  @include font-settings ($ff-inter, 400, 16px, 1.5);

  @include vp-767 {
    font-size: 13px;
  }
}

.updates-comments__reply-btn {
  @include font-settings ($ff-inter, 500, 16px, 1.5);
  background-color: transparent;
  border: none;
  margin-left: auto;
  color: $color-default-white;
  cursor: pointer;

  @include vp-767 {
    position: absolute;
    bottom: 26px;
    left: 13px;
  }

  svg {
    fill: $color-default-white;
    margin-right: 5px;
  }

  &:hover {
    color: $color-accent;
  }
}

.updates-comments__text {
  @include font-settings ($ff-inter, 400, 16px, 1.71);

  p {
    @include vp-767 {
      font-size: 14px;
      margin-bottom: 15px;
    }
  }
}

.updates-comments__reply-block {
  display: flex;
  max-width: 730px;
  background-color: #262931;
  border-radius: 8px;
  padding: 24px;
  display: flex;
  margin-bottom: 20px;

  @include vp-767 {
    flex-direction: column;
    padding: 20px;
  }
}

.updates-comments__reply-author-img {
  position: relative;
  width: 40px;
  height: 40px;
  background-color: #1A1D25;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 15px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include vp-767 {
    margin-bottom: 22px;
  }
}

.updates-comments__reply-block {
  form {
    display: flex;

    @include vp-767 {
      flex-direction: column;
    }
  }

  textarea {
    @include font-settings ($ff-inter, 400, 16px, 1.5);
    min-width: 506px;
    min-height: 96px;
    color: $color-default-white;
    background-color: #262931;
    border: 1px solid #FFC600;
    border-radius: 8px;
    margin-right: 16px;
    padding: 10px 20px;
    caret-color: #FFC600;

    &::-webkit-scrollbar {
      display: none;
    }

    @include vp-1023 {
      min-width: 249px;
      width: 100%;
      margin-bottom: 22px;
    }
  }
}

.updates-comments__submit-btn {
  @include font-settings ($ff-inter, 500, 14px, 1.71);
  min-width: 104px;
  max-height: 48px;
  background: #FFFFFF;
  color: #262931;
  border: none;
  border-radius: 8px;
  cursor: pointer;

  &:hover {
    background-color: #D1D2D3;
  }

  &:active {
    opacity: 0.6;
  }

  @include vp-767 {
    width: 104px;
    height: 48px;
    border-radius: 8px;
  }
}
