.upload {
  display: flex;

  @include vp-767 {
    flex-direction: column;
  }
}

.upload__logo {
  margin-right: 50px;

  @include vp-767 {
    margin-right: 0;
    margin-bottom: 38px;
  }
}

.upload h3 {
  @include font-settings ($ff-inter, 400, 14px, 1.5);
  margin-bottom: 9px;

  @include vp-767 {
    font-size: 12px;
    line-height: 1.67;
    letter-spacing: -0.154px;
  }
}

.upload p {
  @include font-settings ($ff-inter, 400, 14px, 1.5);
  text-align: center;
  margin: 0;
}

.upload__drop-zone {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px dashed #454954;
  border-radius: 5px;
  width: 325px;
  height: 179px;
  padding: 20px 10px;

  @include vp-767 {
    width: 100%;
    padding: 18px 10px 9px;
  }
}

/* upload content */

.upload__content {
  display: none;
}

.upload__content.visible {
  display: block;
}

.upload__content p {
  padding: 0 65px;
  margin-bottom: 15px;

  @include vp-767 {
    padding: 0 45px;
  }
}

.upload input {
  height: 1px;
  opacity: 0;
}

.upload label {
  @include vp-767 {
    width: 135px;
    margin-bottom: 21px;
  }

  b {
    @include font-settings ($ff-inter, 500, 16px, 1.38);
    color: #000;
    display: block;
    width: 150px;
    text-align: center;
    background-color: $color-accent;
    border-radius: 5px;
    margin: 0 auto 12px;
    padding: 14px;

    @include vp-767 {
      width: 135px;
      margin-bottom: 22px;
    }
  }
}

.upload input[type=file]:focus+label {
  outline: 1px solid #ffffff;
  outline: -webkit-focus-ring-color auto 5px;
}

.upload__content span {
  @include font-settings ($ff-inter, 400, 13px, 1.69);
  opacity: 0.5;
  font-style: italic;
  display: block;
  text-align: center;

  @include vp-767 {
    font-size: 12px;
    line-height: 1.83;
  }
}


/* upload-preloader  */

.upload__preloader {
  display: none;
}

.upload__preloader.visible {
  display: block;
}

.upload__preloader-content {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 18px;
}

.upload__preloader-content .upload__filename {
  margin-right: 7px;
}

.upload__preloader>p,
.upload__preloader-percentage {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 40px;
  line-height: 1.2;
  margin-bottom: 31px;
}

/* progress bar */

progress {
  width: 300px;
  height: 11px;
  background: #977b02;
  border-radius: 10px;

  @include vp-767 {
    width: 260px;
  }
}

progress::-webkit-progress-bar {
  width: 300px;
  height: 11px;
  background: #977b02;
  border-radius: 10px;

  @include vp-767 {
    width: 260px;
  }
}

progress::-moz-progress-bar {
  height: 11px;
  background: #FCCD03;
  border-radius: 10px;
}

progress::-webkit-progress-value {
  height: 11px;
  background: #FCCD03;
  border-radius: 10px;
}

/* upload__status  */

.upload__status {
  display: none;
}

.upload__status.visible {
  display: flex;
  align-items: center;
  justify-content: center;
}

.upload__status svg {
  margin-right: 11px;
}

.upload__status .upload__filename {
  margin: 0;
  margin-right: 6px;
}

.upload__reset-btn {
  position: relative;
  width: 15px;
  height: 15px;
  background-color: #282c37;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.upload__reset-btn::before,
.upload__reset-btn::after {
  position: absolute;
  content: "";
  top: 7px;
  left: 4px;
  background-color: #85899d;
  width: 7px;
  height: 1px;
}

.upload__reset-btn::before {
  transform: rotate(45deg);
}

.upload__reset-btn::after {
  transform: rotate(135deg);
}

.upload .error-message {
  position: absolute;
  display: inline-block;
  bottom: -20px;
  left: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.69;
  color: #ff0000;
}

.hovered {
  background-color: #262832;
  border: 1px dashed #A3A5A8;
}

.drop-zone-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
}

.drop-zone-mini {
  width: 100%;
  padding: 177px 0 0;
  cursor: pointer;
}
