.rally__video,
.dubai__video {
  .video-wrapper {
    position: relative;
    display: flex;
    justify-content: center;
  }

  .video {
    position: relative;
    width: 100%;
    height: 678px;
    max-height: 678px;

    @include vp-767 {
      width: 100%;
      min-width: 290px;
      min-height: 210px;
    }
  }

  .video__link {
    display: block;


    @include vp-767 {
      min-height: 210px;
    }

    img {
      height: 100%;
      object-fit: cover;
    }
  }

  .video.is-active .video__btn {
    display: flex;
  }

  .video__poster {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
  }

  .video__btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 233px;
    height: 233px;
    border: none;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(12px);
    cursor: pointer;

    img {
      margin-left: 20px;
      display: block;
    }

    &:hover {
      transform: translate(-50%, -50%) scale(1.1);
    }
  }
}
