.article__content {
  position: relative;
  display: grid;
  grid-template-columns: auto 360px;
  column-gap: 40px;
  row-gap: 14px;
  padding: 50px 38px 37px;
  background-color: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;

  @include vp-1279 {
    grid-template-columns: auto 290px;
    column-gap: 15px;
  }

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    padding: 20px 14px;
  }
}

.article__left-col {
  margin-bottom: 10px;

  @include vp-1023 {
    display: flex;
    flex-direction: column;
  }
}

.article__cover {
  position: relative;
  width: 100%;
  height: 300px;
  border-radius: 20px;
  margin-bottom: 25px;
  overflow: hidden;

  @include vp-1023 {
    height: 210px;
    border-radius: 14px;
    order: -2;
    margin-bottom: 17px;
  }

  &::before {
    position: absolute;
    content: "";
    width: calc(100% + 2px);
    height: calc(100% + 2px);
    background: linear-gradient(180deg, rgba(38, 41, 49, 0) 15%, #262931 100%);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  p {
    @include font-settings ($ff-inter, 500, 14px, 1);
    letter-spacing: 0.3px;
    text-transform: uppercase;
    position: absolute;
    top: 22px;
    right: 21px;
    background: rgba(0, 0, 0, 0.35);
    backdrop-filter: blur(5px);
    border-radius: 5px;
    padding: 5px;
    max-height: fit-content;

    @include vp-767 {
      bottom: 19px;
      right: 19px;
    }
  }
}

.article__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 4px;

  @include vp-1023 {
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 3px;
  }
}

.article__author {
  display: flex;
  align-items: center;

  @include vp-1023 {
    margin-bottom: 15px;
  }

  p,
  a {
    @include font-settings ($ff-inter, 400, 14px, 1.1);
    color: #aaaaaa;
    letter-spacing: 0.96px;
    text-transform: uppercase;

    @include vp-1023 {
      font-size: 14px;
      margin-bottom: 8px;
    }

    @include vp-767 {
      width: 100%;
      margin: 0;
    }
  }
}

.article__author-img {
  position: relative;
  width: 36px;
  height: 36px;
  background: url("../img/admin-icon.png") center no-repeat;
  background-size: cover;
  border-radius: 50%;
  margin-right: 10px;
  overflow: hidden;

  @include vp-1023 {
    margin-right: 13px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.article__tags-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
  flex-wrap: wrap;

  @include vp-1023 {
    justify-content: flex-start;
  }
}

.article__tags-item {
  margin-left: 13px;
  margin-bottom: 6px;
  border: 1px solid #FFFFFF;
  border-radius: 20px;
  opacity: 0.5;
  padding: 0 15px 2px;

  @include vp-1023 {
    margin-left: 0;
    margin-right: 4px;
  }

  span {
    @include font-settings ($ff-inter, 400, 14px, 1);
    letter-spacing: 0.3px;
  }
}

.article__text-heading {
  position: absolute;
  display: flex;
  align-items: flex-end;
  top: 52px;
  left: 40px;
  width: 60%;
  height: 295px;
  padding: 10px 20px;

  @include vp-1279 {
    top: 20px;
    width: 90%;
  }

  @include vp-1023 {
    position: relative;
    display: block;
    width: 100%;
    height: fit-content;
    padding: 0;
    top: 0;
    left: 0;
    order: -1;
    margin-bottom: 8px;
    padding-left: 5px;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 35px, 0.99);
    letter-spacing: 0.97px;
    text-transform: uppercase;

    @include vp-1023 {
      font-size: 25px;
      line-height: 1.39;
    }
  }
}

.article__text {
  margin-bottom: 8px;
  padding-left: 6px;
  padding-top: 17px;

  @include vp-1023 {
    padding-top: 6px;
    padding-left: 4px;
  }

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin-bottom: 22px;
    word-wrap: break-word;
  }

  h2 {
    @include font-settings ($ff-inter, 400, 26px, 1.5);
    margin: 0 0 7px;
    color: $color-accent;
  }

  h3 {
    @include font-settings ($ff-inter, 400, 24px, 1.5);
    margin: 0 0 7px;
    color: $color-accent;
  }

  h4 {
    @include font-settings ($ff-inter, 400, 22px, 1.5);
    margin: 0 0 7px;
    color: $color-accent;
  }

  ul,
  ol {
    margin: 0 0 10px;
    padding: 0 0 0 23px;
  }

  li {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
  }

  img {
    width: 100%;
    height: auto;
    object-fit: cover;
    margin-bottom: 16px 0 25px;
  }

  a {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    font-style: normal;
    color: $color-accent;
    text-decoration: underline;
    margin-bottom: 12px;
  }

  code {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  pre {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    white-space: pre-wrap;
    word-wrap: break-word;
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  blockquote {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    padding-left: 16px;
    border-left: 2px solid #DEE2E6;
    margin: 10px 0;
  }

  hr {
    height: 1px;
    margin: 12px 0;
    background-color: #DEE2E6;
    border: none;
  }
}

.article__image {
  width: 100%;
  height: 400px;
  border-radius: 20px;
  margin: 16px 0 25px;
  overflow: hidden;

  @include vp-767 {
    height: 200px;
    margin: 22px 0 10px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.article__filters {
  margin-bottom: 27px;

  @include vp-1279 {
    display: none;
  }
}

.article__news {
  margin-top: 73px;

  @include vp-1023 {
    display: none;
  }

  h3 {
    margin-bottom: 14px;
  }
}

.article .adblock-295x430 {
  display: none;

  @include vp-1279 {
    display: block;
  }
}

.article__comments {
  grid-column: 1 / 3;

  @include vp-767 {
    padding-top: 8px;
  }

  h2 {
    @include font-settings ($ff-inter, 600, 18px, 1.56);
    margin-bottom: 11px;
  }

  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
    display: inline-block;
    vertical-align: middle;
    min-width: 33px;
    height: 30px;
    background-color: #262931;
    border-radius: 10px;
    text-align: center;
    opacity: 0.5;
    margin-left: 10px;
    padding: 8px 12px;
  }
}

.article__comments-list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;

  @include vp-1023 {
    justify-content: center;
  }
}

.article__comments-item {
  max-width: calc(50% - 20px);
  background-color: #262931;
  border-radius: 20px;
  padding: 25px 22px;
  margin-bottom: 21px;

  @include vp-767 {
    max-width: 100%;
    padding: 19px 22px;
    margin-bottom: 10px;
  }

  h3 {
    @include font-settings ($ff-inter, 800, 18px, 1.56);
    margin-bottom: 18px;
  }

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
  }
}

.article__comments-add-btn {
  @include font-settings ($ff-inter, 500, 14px, 1);
  min-width: 233px;
  background-color: $color-accent;
  border: none;
  border-radius: 10px;
  color: $color-default-black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 14px 10px 14px 17px;
  cursor: pointer;

  svg {
    fill: $color-default-black;
    margin-right: 13px;
  }

  &:hover {
    background-color: $color-accent-hover;
  }

  &:active {
    opacity: 0.6;
  }
}
