.company-admin {
  padding-top: 33px;

  @include vp-767 {
    padding: 17px 20px;
  }
}

.company-admin__name {
  input {
    @include font-settings ($ff-inter, 400, 14px, 1.43);
    width: 100%;
    background-color: #262931;
    border: 1px solid #454954;
    border-radius: 6px;
    padding: 9px 12px;
    margin-bottom: 33px;

    @include vp-767 {
      margin-bottom: 22px;
    }
  }

  label {
    position: relative;
  }

  .error-message {
    bottom: -23px;
  }

  h3 {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin-bottom: 9px;

    @include vp-767 {
      font-size: 14px;
      letter-spacing: -0.154px;
    }
  }
}

// EDITOR

.company-admin__edit-section {
  display: grid;
  grid-template-columns: auto 400px;
  column-gap: 20px;
  margin-top: 28px;
  margin-right: 105px;
  margin-bottom: 20px;

  @include vp-1279 {
    display: flex;
    flex-direction: column;
    margin-top: 26px;
    margin-right: 0;
  }
}

.company-admin__text {
  min-width: 715px;
  margin-top: 8px;

  @include vp-1279 {
    margin-bottom: 40px;
  }

  @include vp-767 {
    min-width: 290px;
    margin-bottom: 20px;
  }
}

.company-admin__links {
  display: flex;
  flex-direction: column;

  h3 {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin-bottom: 9px;

    @include vp-767 {
      font-size: 14px;
      line-height: 1.43;
      letter-spacing: -0.154px;
    }
  }

  input {
    @include font-settings ($ff-inter, 400, 14px, 1.43);
    width: 400px;
    height: 40px;
    background-color: #262931;
    border: 1px solid #454954;
    border-radius: 6px;
    padding: 10px 12px;
    margin-bottom: 14px;

    @include vp-1279 {
      width: 100%;
    }
  }
}

.company-admin__links-block--first {
  display: flex;
  flex-direction: column;
  margin-bottom: 37px;

  @include vp-767 {
    margin-bottom: 8px;
  }

  label {
    position: relative;
    width: 400px;

    @include vp-1279 {
      width: 100%;
    }
  }

  .error-message {
    bottom: 0;
  }

  .company-admin__add-link {
    @include font-settings ($ff-inter, 400, 14px, 1);
    position: absolute;
    top: 4px;
    right: 4px;
    width: 38px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #37393F;
    color: $color-default-white;
    border: 0.75px solid #37393F;
    box-shadow: 0px 1px 0px rgba(17, 27, 43, 0.05);
    border-radius: 5px;
    cursor: pointer;

    &:hover {
      background: #56585E;
    }
  }
}

.company-admin__links-block--second {
  label {
    position: relative;
  }

  .error-message {
    bottom: -23px;
  }
}

.company-admin__links-block--third {

  fieldset {
    padding: 0;
    border: none;
    margin-bottom: 20px;
  }

  .company-admin__add-link {
    @include font-settings ($ff-inter, 400, 14px, 1);
    width: 38px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #37393F;
    color: $color-default-white;
    border: 0.75px solid #37393F;
    box-shadow: 0px 1px 0px rgba(17, 27, 43, 0.05);
    border-radius: 5px;
    margin-bottom: 10px;
    justify-self: flex-end;
    cursor: pointer;

    &:hover {
      background: #56585E;
    }
  }
}

.company-admin {
  input {
    &:hover {
      border: 1px solid #FCCD03;
    }
  }
}

.ca-wysiwig {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.ca-wysiwig__btn {
  height: 28px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: $color-default-white;
  padding: 0;

  span {
    @include font-settings ($ff-inter, 400, 14px, 1.4);
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 10px;
  }

  .ca-wysiwig__color {
    width: 20px;
    height: 20px;
    background-color: $color-default-white;
    border-radius: 4px;
  }
}

.company-admin__submit-btn {
  @include font-settings ($ff-inter, 500, 14px, 1);
  width: 202px;
  height: 47px;
  background-color: $color-accent;
  border: none;
  border-radius: 10px;
  color: #14161B;
  margin-bottom: 3px;
  cursor: pointer;

  @include vp-767 {
    margin-top: 4px;
  }

  &:hover {
    background-color: $color-accent-hover;
  }

  &:active {
    opacity: 0.6;
  }
}
