*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Inter", "Arial", sans-serif;
  color: #ffffff;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  color: #ffffff;
  background-color: #14161B;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
}

button {
  user-select: none;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #000000;
  -webkit-text-fill-color: #ffffff;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.error-message {
  display: none;
}

.error-message.active {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 1;
  color: #ffffff;
  display: inline-block;
  position: absolute;
  bottom: 3px;
  left: 5px;
  color: #ff4e64;
}

*:active {
  -webkit-tap-highlight-color: transparent;
}

a:active {
  opacity: 0.6;
}

input[type=email].invalid,
input[type=number].invalid,
input[type=password].invalid,
input[type=tel].invalid,
input[type=text].invalid,
input[type=url].invalid {
  border: 1px solid #ff4e64;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.w-box {
  margin-top: 0 !important;
}

.container {
  width: 100%;
  max-width: 1245px;
  margin: 0 auto;
}
@media (max-width: 1279px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.wc-intro__decor {
  animation: pulsate 3.9s infinite linear;
}
.wc-intro__decor--1 {
  animation-delay: 1.2s;
}
.wc-intro__decor--2 {
  animation-delay: 0.8s;
}
.wc-intro__decor--3 {
  animation-delay: 0.5s;
}
.wc-intro__decor--4 {
  animation-delay: 0.2s;
}
.wc-intro__decor--5 {
  animation: none;
}

.wc-intro__circle {
  animation-duration: 2s;
}
.wc-intro__circle:nth-child(-n+3) {
  animation-name: move-back;
}
.wc-intro__circle:nth-child(n+5) {
  animation-name: move-forward;
}
.wc-intro__circle--main {
  animation: none;
}
.wc-intro__circle--l {
  animation-delay: 0.2s;
}
.wc-intro__circle--m {
  animation-delay: 0.4s;
}
.wc-intro__circle--s {
  animation-delay: 0.6s;
}

.slideleft.effect {
  animation-duration: 1s;
  animation-name: slideleft;
  animation-timing-function: ease-in-out;
}

.slideright.effect {
  animation-duration: 1s;
  animation-name: slideright;
  animation-timing-function: ease-in-out;
}

.slidedown {
  animation-duration: 2s;
  animation-name: slidedown;
  animation-timing-function: ease-in-out;
}
.slidedown--2 {
  animation-delay: 0.2s;
}
.slidedown--3 {
  animation-delay: 0.3s;
}
.slidedown--4 {
  animation-delay: 0.4s;
}
.slidedown--5 {
  animation-delay: 0.5s;
}

.start.slideup {
  opacity: 0.2;
  transition: all 0.5s ease-in;
  transform: translateY(100px);
  will-change: transform;
}
.start.slideup--slow {
  transition: all 0.8s ease-in;
}
.start.slideup--slow-2 {
  transition: all 1.2s ease-in;
}

.slideup.effect {
  transform: translateY(0);
  opacity: 1;
}

.start.grow {
  opacity: 0.2;
  transition: all 0.7s ease-in;
  transform: scale(1, 0);
  will-change: transform;
}

.effect.grow {
  transform: scale(1, 1);
  opacity: 1;
}

.fadein {
  animation-duration: 2s;
  animation-name: fadein;
  animation-timing-function: ease-in-out;
}

@keyframes pulsate {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.2;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes move-forward {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes move-back {
  0% {
    opacity: 0;
    transform: translateX(20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes slideleft {
  from {
    transform: translateX(-100vw);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes slideright {
  from {
    transform: translateX(100vw);
  }
  to {
    transform: translateX(0);
  }
}
@keyframes slidedown {
  from {
    transform: translateY(-100vh);
  }
  to {
    transform: translateY(0);
  }
}
