.catalog {
  position: relative;
  background-color: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 52px 40px 20px;

  @include vp-1279 {
    padding: 14px;
  }
}

.catalog__heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 35px;
  padding-right: 5px;

  @include vp-1279 {
    margin-bottom: 16px;
  }

  @include vp-1023 {
    flex-direction: column;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 35px, 1);
    letter-spacing: 0.96px;
    text-transform: uppercase;

    @include vp-1023 {
      font-size: 25px;
      line-height: 1.2;
      padding-left: 5px;
      margin-bottom: 10px;
    }
  }
}

.catalog__btn-wrapper {
  display: flex;
}

.catalog__view-btn {
  width: 47px;
  height: 47px;
  background-color: #262931;
  border: none;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0.6;
  cursor: pointer;

  &--tiles {
    margin-right: 9px;
  }

  &--list {
    margin-right: 17px;
  }

  &:hover,
  &.active {
    background-color: #535761;
  }

  @include vp-1023 {
    display: none;
  }
}

.catalog__add-btn {
  @include font-settings ($ff-inter, 500, 14px, 1);
  min-width: 190px;
  background-color: $color-accent;
  border: none;
  border-radius: 10px;
  color: $color-default-black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 22px 12px 16px;
  cursor: pointer;

  svg {
    fill: $color-default-black;
    margin-right: 12px;
  }

  &:hover {
    background-color: $color-accent-hover;
  }

  &:active {
    opacity: 0.6;
  }
}

.catalog__content {
  display: grid;
  grid-template-columns: auto 360px;
  column-gap: 40px;
  row-gap: 15px;

  @include vp-1279 {
    grid-template-columns: auto 290px;
    column-gap: 15px;
  }

  @include vp-1023 {
    grid-template-columns: auto;
  }
}

.catalog__list {
  display: flex;

  @include vp-1023 {
    justify-content: center;
  }

  &--tile {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }

  &--list {
    flex-direction: column;

    .catalog__item {
      max-width: 100%;
      margin-bottom: 18px;
    }

    .catalog__card {
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
      padding: 13px 17px 10px 18px;
    }

    .catalog__title {
      margin: 0;

    }

    .catalog__cover {
      min-height: 53px;
      max-width: 100%;
      padding: 10px 0 0 0;
      border-radius: 0;


      img,
      &::before {
        display: none;
      }
    }

    .catalog__logo-block {
      order: -1;
      margin-right: 20px;

      img {
        display: block;
      }
    }

    .catalog__title {
      justify-content: flex-start;
      margin-bottom: 8px;
    }

    .catalog__info-block {
      justify-content: flex-start;
      margin: 0;
    }

    .catalog__rating {
      order: -1;
      margin-right: 45px;
    }

    .catalog__text-block {
      min-width: 100%;
    }
  }
}

.catalog__item {
  max-width: 360px;
  margin-bottom: 22px;

  @include vp-1279 {
    max-width: 290px;
    margin: 0 5px 15px;
  }
}

.catalog__card {
  position: relative;
  height: 100%;
  background-color: #262931;
  border-radius: 20px;
  padding: 19px 20px 14px;
  display: flex;
  flex-direction: column;

  @include vp-1023 {
    padding: 19px 16px 10px;
  }

  &:hover {
    box-shadow: 0px 0px 20px rgba(10, 57, 184, 0.25);
  }

  &:active {
    opacity: 0.6;
  }

  .promo__marker {
    display: none;
  }
}

.catalog__card.promo {
  overflow: hidden;

  .promo__marker {
    @include font-settings ($ff-inter, 400, 11px, 1.5);
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    background: rgb(118, 119, 124, 0.5);
    padding: 0.5px 25px;
    border-radius: 20px 0 0 0;

  }
}

.catalog__title {
  @include font-settings ($ff-inter, 700, 20px, 1.74);
  letter-spacing: 0.96px;
  text-transform: uppercase;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;

  @include vp-1279 {
    font-size: 16px;
    margin-bottom: 6px;
  }
}

.catalog__company-link {
  z-index: 10;
}

.catalog__cover {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 10px 14px;
  max-width: 320px;
  min-height: 73px;
  border-radius: 20px;
  overflow: hidden;

  @include vp-1023 {
    padding: 7px;
    max-height: 59px;
    min-height: 59px;
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    max-width: 320px;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(20, 22, 27, 0.7);
    z-index: 10;
  }
}

.catalog__logo-block {
  max-width: 111px;
  min-width: 53px;
  height: 53px;
  background: rgba(255, 255, 255, .35);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 13px;
  z-index: 10;

  @include vp-1023 {
    max-width: 90px;
    height: 43px;
  }

  img {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.catalog__info-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;

  @include vp-1023 {
    margin-bottom: 14px;
  }
}

.catalog__link {
  @include font-settings ($ff-inter, 500, 14px, 1.5);
  display: block;
  position: relative;
  padding: 9px 35px 9px 9px;
  border-radius: 10px;

  &::after {
    position: absolute;
    content: url("../img/svg/share-box.svg");
    top: 9px;
    right: 9px;
    width: 20px;
    height: 20px;
  }

  &:hover {
    color: $color-accent;
    background-color: #1A1D25;
  }

  &:hover::after {
    content: url("../img/svg/share-box-hover.svg");
  }

  @include vp-1279 {
    font-size: 12px;
    padding-left: 19px;

    &::before {
      transform: scale(0.9, 0.9);
      top: 3px;
      left: 0px;
    }
  }
}

.catalog__rating {
  display: flex;
  align-items: center;

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin-right: 10px;

    @include vp-1279 {
      font-size: 11px;
      margin-right: 8px;
    }
  }

  svg {
    &:last-of-type {
      margin-right: 0;
    }

    @include vp-1279 {
      width: 11px;
      height: 11px;
      margin-right: 6px;
    }
  }
}

.catalog__text-block {
  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
    margin-bottom: 10px;

    @include vp-1023 {
      max-height: 54px;
      font-size: 12px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      @supports (display: -webkit-box) {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        white-space: normal;
      }
    }
  }

  h2 {
    @include font-settings ($ff-inter, 700, 20px, 1.5);
    opacity: 0.5;
  }

  h3 {
    @include font-settings ($ff-inter, 700, 18px, 1.5);
    opacity: 0.5;
  }

  h4 {
    @include font-settings ($ff-inter, 400, 16px, 1.5);
    opacity: 0.5;
  }

  ul,
  ol {
    margin: 0 0 10px;
    padding: 0 0 0 23px;
  }

  li {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
  }

  img {
    width: 100%;
    height: auto;
    object-fit: cover;
    margin-bottom: 10px;
  }

  a {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    font-style: normal;
    color: $color-accent;
    text-decoration: underline;
    margin-bottom: 12px;
  }

  code {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  pre {
    opacity: 0.5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }

  blockquote {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
    padding-left: 16px;
    border-left: 2px solid #DEE2E6;
    margin: 10px 0;
  }

  hr {
    height: 1px;
    margin: 12px 0;
    background-color: #DEE2E6;
    border: none;
  }
}

.catalog__details-btn {
  display: block;
  @include font-settings ($ff-inter, 500, 14px, 1);
  color: $color-default-black;
  letter-spacing: 0.96px;
  text-transform: uppercase;
  background-color: $color-accent;
  width: fit-content;
  border: none;
  border-radius: 10px;
  padding: 12px 20px;
  cursor: pointer;
  margin-top: auto;

  &:hover {
    background-color: $color-accent-hover;
  }
}

.catalog__show-more-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  min-height: 47px;
  background-color: #262931;
  border: none;
  border-radius: 10px;
  color: $color-default-white;
  cursor: pointer;

  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
  }

  svg {
    margin-right: 17px;
  }

  &:hover {
    background-color: #76777C;
    color: $color-default-white;
  }

  &:active {
    opacity: 0.5;
  }
}

.catalog__filters.filters {
  padding: 25px 20px 15px;

  .filters__reset-btn {
    top: 27px;
    right: 16px;
  }

  .filters__group {
    padding-top: 11px;
  }

  h3 {
    margin-bottom: 11px;
  }

  .filters__search {
    margin-bottom: 23px;
  }

  .filters__list {
    height: 160px;
    overflow: hidden;
    margin-bottom: 0;
    padding-top: 5px;

    &.active {
      height: auto;
      margin-bottom: 23px;
    }
  }

  .filters__show-more-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border: none;
    border-radius: 10px;
    margin: 0 auto;
    margin-bottom: 20px;
    padding: 5px 10px 7px;
    cursor: pointer;

    span {
      @include font-settings ($ff-inter, 400, 14px, 1);
      color: $color-default-white;
      margin-right: 7px;
    }

    .collapse {
      display: none;
    }

    &.active {

      .collapse {
        display: inline;
      }

      .expand {
        display: none;
      }

      svg {
        transform: rotate(180deg) translateY(-2px);
      }
    }

    &:hover {
      background-color: #1A1D24;
      transform: scale(1.1);
    }
  }

  .filters__group.select {
    border: none;
  }

  .select__title {
    @include font-settings ($ff-inter, 400, 14px, 1.1);
    opacity: 0.5;
    margin-bottom: 11px;
  }

  .filters__submit-btn {
    margin-top: 10px;
  }
}

// mobile

.catalog__filters {
  @include vp-1023 {
    display: none;
  }
}

.catalog__filters.active {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  border-radius: 20px;
  z-index: 10000;
}

.catalog__filter-show-btn {
  display: none;

  @include vp-1023 {
    display: block;
    width: 47px;
    height: 47px;
    background-color: #262931;
    border: none;
    border-radius: 10px;
    cursor: pointer;

    &:hover {
      background-color: #535761;
    }
  }
}

.catalog__filter-hide-btn {
  display: none;

  @include vp-1023 {
    @include font-settings ($ff-inter, 500, 14px, 1.43);
    display: block;
    color: #000000;
    position: fixed;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    min-width: 109px;
    height: 40px;
    background-color: $color-accent;
    border: none;
    border-radius: 10px;

    &:hover {
      background-color: $color-accent-hover;
    }
  }
}

.catalog__right-col .adblock {
  margin-bottom: 20px;
}
