.accordion {
  position: relative;

  @include vp-767 {

    &::after {
      position: absolute;
      content: "";
      width: 100%;
      height: 1px;
      background-color: #5C5F65;
      bottom: 0;
      left: 0;
    }
  }
}

.accordion__button {
  display: none;

  @include vp-767 {
    display: block;
    position: relative;
    cursor: pointer;
    text-align: left;
    width: 100%;
    height: 45px;
    border: none;
    background-color: transparent;
    @include font-settings ($ff-inter, 600, 14px, 1);
    text-transform: uppercase;

    &::after {
      position: absolute;
      content: url("../img/svg/select-control.svg");
      top: 15px;
      right: 7px;
      transform: scale(1, 1);
      transition: transform 0.3s ease-in-out;
    }
  }
}

.accordion__button.open {
  &::after {
    transform: scale(1, -1);
    transition: transform 0.3s ease-in-out;
  }
}

.accordion__body {
  @include vp-767 {
    max-height: 0;
    padding-left: 6px;
    overflow: hidden;
    transition: max-height 0.3s ease-in-out;
  }
}
