.team {
  display: flex;
  align-items: center;
}

.team__description {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 59px;

  @include vp-767 {
    margin-bottom: 27px;
    padding: 0 5px;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 48px, 1.33);
    color: #D1D2D3;
    margin-bottom: 39px;
    padding-top: 29px;

    @include vp-767 {
      margin-bottom: 28px;
      padding-top: 3px;
    }
  }

  p {
    @include font-settings ($ff-inter, 500, 16px, 1.62);
    color: #6C757D;
    max-width: 70%;
    margin-bottom: 27px;

    @include vp-767 {
      font-size: 13px;
      max-width: 100%;
      margin-bottom: 22px;
      text-align: left;
    }
  }

  i {
    @include font-settings ($ff-inter, 500, 20px, 1.6);
    color: #A1A2A4;
    font-style: italic;
    max-width: 80%;
    margin-top: 31px;

    @include vp-767 {
      font-size: 15px;
      max-width: 100%;
      margin-top: 17px;
      text-align: left;
    }
  }
}

.team__list {
  display: flex;
  flex-wrap: wrap;
  max-width: 1140px;

  @include vp-1279 {
    max-width: 761px;
  }

  @include vp-1023 {
    max-width: 350px;
  }
}

.team__item {
  width: 350px;
  margin: 0 15px 32px;

  @include vp-1023 {
    margin: 0 0 20px;
  }
}

.team__logo-icon {
  display: none;
  position: absolute;
  justify-content: center;
  align-items: center;
  bottom: 10px;
  right: 10px;
  width: 53px;
  height: 53px;
  border-radius: 50%;
  background: radial-gradient(88.73% 88.73% at 50.29% 11.27%, #FEE67E 0%, #FCCD03 100%);
  z-index: 1000;
}

.team__item:hover .team__logo-icon {
  display: flex;
}

.team__info {
  padding: 27px 25px 26px;
  background: #20232C;
  opacity: 0.9;
  border-radius: 30px;

  h3 {
    @include font-settings ($ff-inter, 700, 26px, 1);
    color: #D1D2D3;
    margin-bottom: 9px;
  }

  p {
    @include font-settings ($ff-inter, 500, 14px, 1.7);
    color: #D1D2D3;
    margin-bottom: 14px;
  }

}

.team__btns {
  display: flex;
}

.team__btn {
  @include font-settings ($ff-inter, 500, 14px, 1.7);
  color: #D1D2D3;
  margin-right: auto;
  min-width: 149px;
  padding: 7px;
  background: #262832;
  border: 1px solid rgba(118, 119, 124, 0.4);
  border-radius: 7px;
  cursor: pointer;
}

.team__btn:hover {
  background: #1D1F2B;
}

.team__contacts {
  display: flex;

  a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 38px;
    height: 38px;
    background: #262832;
    border-radius: 7px;
    color: $color-default-white;

    svg>use {
      fill: $color-default-white;
    }
  }

  a:not(:last-child) {
    margin-right: 8px;
  }

  a:hover {
    background: #2F313D;

    svg>use {
      fill: $color-accent;
    }
  }
}

.team-popup-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: 0.5s all;
  z-index: 1000;
}

.team-popup-overlay.active {
  display: block;
  opacity: 1;
  pointer-events: all;
  transition: 0.5s all;
  overflow-x: hidden;
  overflow-y: scroll;
}

.team-popup {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.5s all;
  min-height: fit-content;
  min-width: 350px;
  width: 78%;
  padding: 15px 100px 15px 19px;
  background: #262832;
  border-radius: 30px;
  z-index: 10000;

  @include vp-1023 {
    top: 2%;
    left: 50%;
    min-width: 350px;
    min-height: 100vh;
    background: #262832;
    border-radius: 30px;
    margin: 0;
    padding: 15px;
    padding-bottom: 40px;
  }
}

.team-popup.active {
  transform: translate(-50%, -50%) scale(1);
  transition: 0.5s all;

  @include vp-1023 {
    transform: translate(-50%, 0);
  }
}

.team-popup__content-item {
  display: none;
}

.team-popup__content-item.active {
  display: flex;
  align-items: center;

  @include vp-1023 {
    flex-direction: column;
  }
}

.team-popup__image {
  position: relative;
  min-width: 285px;
  height: 350px;
  border-radius: 25px;
  overflow: hidden;
  margin-right: 47px;

  @include vp-1023 {
    min-width: 319px;
    margin-right: 0;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &--kesha {
    img {
      object-fit: contain;
    }
  }
}

.team-popup .team__logo-icon {
  display: flex;
}

.team-popup__heading {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-top: 10px;

  @include vp-1023 {
    flex-direction: column
  }

  h3 {
    @include font-settings ($ff-inter, 700, 38px, 1.68);
    color: #D1D2D3;
  }

  p {
    @include font-settings ($ff-inter, 600, 16px, 1.5);
    color: #D1D2D3;
    margin-bottom: 11px;
  }
}

.team-popup__contacts {
  padding-top: 16px;

  a {
    background: #20232F;
  }
}

.team-popup__description {
  padding-bottom: 20px;

  p {
    @include font-settings ($ff-inter, 500, 14px, 1.6);
    color: #6C757D;
  }
}

.team-popup__close-btn {
  position: absolute;
  width: 44px;
  height: 44px;
  top: 10px;
  right: 15px;
  background: transparent;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  svg {
    pointer-events: none;
  }

  @include vp-1023 {
    display: none;
  }
}

.team-popup__close-btn-mob {
  display: none;

  @include vp-1023 {
    @include font-settings ($ff-inter, 500, 14px, 1.43);
    display: block;
    position: fixed;
    bottom: 0;
    left: calc(50% - 2px);
    transform: translate(-50%, 0);
    width: 78%;
    min-width: 354px;
    padding: 10px;
    background: #15171E;
    border: none;
    border-radius: 10px;
    z-index: 10000;
    cursor: pointer;
  }
}

.team__image {
  position: relative;
  z-index: 1;
  width: 100%;
  padding-bottom: 100%;
  perspective: 1000px;

  .cover {
    position: absolute;
    width: 350px;
    height: 345px;
    overflow: hidden;
    border-radius: 30px;
    z-index: 1;
    top: 0;
    left: 0;
    background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
    background-size: cover;
    perspective-origin: 50% 50%;
    transform-style: preserve-3d;
    transform-origin: top center;
    will-change: transform;
    transform: skewX(.001deg);
    transition: transform .35s ease-in-out;

    // Gloss

    &::after {
      display: block;
      content: '';
      position: absolute;
      z-index: 100;
      top: 0;
      left: 0;
      width: 100%;
      height: 120%;
      background: linear-gradient(226deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.4) 35%, rgba(255, 255, 255, 0.2) 42%, rgba(255, 255, 255, 0) 60%);
      transform: translateY(-20%);
      will-change: transform, opacity;
      transition: all .65s cubic-bezier(0.18, 0.9, 0.58, 1);
      opacity: 0.6;
    }
  }

  // Shadows

  &::before {
    display: block;
    content: '';
    position: absolute;
    top: 5%;
    left: 5%;
    width: 90%;
    height: 90%;
    background: rgba(#000000, 0.5);
    box-shadow: 0 6px 12px 12px rgba(#000000, 0.4);
    will-change: opacity;
    transform-origin: top center;
    transform: skewX(.001deg);
    transition: transform .35s ease-in-out, opacity .5s ease-in-out;
  }

  &:hover::before {
    opacity: 0.6;
    transform: rotateX(7deg) translateY(-6px) scale(1.05);
  }
}

.team__item:hover .cover {
  transform: rotateX(7deg) translateY(-6px);

  &::after {
    transform: translateY(0%);
    opacity: 1;
  }
}
