.header {
  background-color: #1E2228;
  position: fixed;
  min-height: 70px;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.header .container {
  padding-top: 15px;
  padding-bottom: 10px;
  display: flex;
  justify-content: space-between;

  @include vp-767 {
    padding-bottom: 13px;
  }
}

.header__logo-wrapper {
  display: flex;
  align-items: center;

  &>span {
    @include font-settings ($ff-inter, 600, 14px, 1);
    padding-right: 9px;
  }
}

.header__logo {
  width: 190px;
  height: 42px;
  padding-top: 5px;
  margin-right: 9px;

  @include vp-767 {
    width: 127px;
    height: 20px;
    margin-left: 15px;
    padding: 0;
  }

  img {
    width: 100%;
    height: auto;
    object-fit: contain;
  }

  &>a {
    @include vp-767 {
      display: none;
    }
  }
}

.header__logo-btn-mob {
  display: none;

  @include vp-767 {
    display: block;
    background: transparent;
    border: none;
    padding: 0;
    cursor: pointer;
  }
}

.header__mobile-links {
  display: none;
}

.header__mobile-links.active {
  @include vp-767 {
    display: flex;
    flex-direction: column;
    min-width: fit-content;
    background: #474C53;
    border-radius: 15px;
    margin-top: 8px;
    padding: 10px;

    a {
      @include font-settings ($ff-inter, 500, 14px, 1);
      color: #14161B;
      margin: 5px 0;
      white-space: nowrap;
    }
  }
}

.header__divider {
  display: none;
  position: relative;
  padding: 0 10px;

  &::before {
    position: absolute;
    content: "";
    top: -20px;
    left: 7px;
    width: 1px;
    height: 36px;
    background-color: $color-default-white;
  }

  @include vp-767 {
    display: none;
  }
}

img.forum-img-ru {
  display: none;
}

html[lang="ru"],
html[lang="uk"] {
  img.forum-img-ru {
    display: block;
  }

  img.forum-img-en {
    display: none;
  }
}

.header__forum-link {
  display: none;
  margin-right: 10px;

  @include vp-767 {
    display: none;
  }
}

.header-hover {
  span {
    display: none;
  }
}

.header-hover.opacity {
  opacity: 0.5;
}

.header-hover:hover {
  position: relative;

  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
    display: block;
    color: #14161B;
    position: absolute;
    background: #474C53;
    border-radius: 70px;
    padding: 3px 10px;
    margin-top: 10px;
    white-space: nowrap;

    @include vp-767 {
      display: none;
    }
  }
}

.header__partner-logo {
  max-width: 150px;
  height: 30px;
  display: flex;
  align-items: center;

  @include vp-767 {
    max-width: 60px;
    height: 17px;
  }

  img {
    width: 100%;
    object-fit: contain;

    &:hover {
      opacity: 0.6;
    }
  }
}

.header__search {
  @include vp-1023 {
    display: none;
  }
}
