.header-nav {
  display: none;
}

.header-nav.active {
  display: block;
  position: absolute;
  top: 37px;
  right: 0;
  min-height: 366px;
  background-color: #3E4046;
  border-radius: 6px 0px 6px 6px;
  z-index: 1000;

  @include vp-767 {
    min-width: 360px;
    background: #1E2228;
    border-radius: 0px 0px 20px 20px;
  }
}

.header-nav__content {
  display: grid;
  grid-template-columns: auto 220px;
  column-gap: 62px;
  padding: 27px 59px 19px 44px;

  @include vp-1279 {
    padding: 20px;
    column-gap: 40px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
    min-width: 330px;
    padding: 20px 25px;
  }
}

.header-nav__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: auto;
  padding-top: 33px;

  @include vp-1279 {
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 0;
  }

  @include vp-1023 {
    grid-template-columns: auto;
    padding-top: 10px;
  }

  @include vp-767 {
    margin-bottom: 26px;
  }
}

.header-nav__item {
  position: relative;
  border-radius: 10px;
  width: 261px;
  margin: 0 15px 12px;

  &:hover {
    background-color: #37393F;
  }

  &::before {
    position: absolute;
    content: "";
    width: 33px;
    height: 33px;
    top: 15px;
    left: 11px;
    border: 0.5px solid #E6E6E7;
    border-radius: 11px;
  }

  &--divider {
    display: none;
  }

  &--forum::before {
    background: url("../img/svg/nav-forum.svg") center no-repeat;
  }

  &--courses::before {
    background: url("../img/svg/nav-courses.svg") center no-repeat;
  }

  &--blog::before {
    background: url("../img/svg/nav-blog.svg") center no-repeat;
  }

  &--catalog::before {
    background: url("../img/svg/nav-catalog.svg") center no-repeat;
  }

  &--events::before {
    background: url("../img/svg/nav-events.svg") center no-repeat;
  }

  &--allstars::before {
    background: url("../img/svg/nav-allstars.svg") center no-repeat;
  }

  &--allies::before {
    background: url("../img/svg/nav-allies.svg") center no-repeat;
  }

  @include vp-767 {
    margin: 0;
    min-width: 229px;
    width: 100%;

    &::before {
      left: 12px;
    }
  }
}

.header-nav__link {
  display: block;
  padding: 15px 5px 10px 62px;

  @include vp-1023 {
    padding-bottom: 2px;
  }

  h3 {
    @include font-settings ($ff-inter, 600, 14px, 1);
    text-transform: uppercase;
  }

  p {
    @include font-settings ($ff-inter, 400, 13px, 1);
    padding: 10px 0;
  }
}

.header-nav .adblock-290x76 {
  display: none;

  @include vp-767 {
    display: block;
    order: 1;
  }
}

.header-nav__socials {
  display: none;
}