.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;

  background-color: $color-black-80;
  backdrop-filter: blur(22px);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;

  transition: opacity $trans-modal,
    transform $trans-modal,
    visibility $trans-modal;

  pointer-events: none;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.modal.active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal--preload {
  transition: none;
}

.modal__wrapper {
  position: relative;

  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;

  @include vp-767 {
    padding: 0 15px;
  }
}

.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  cursor: pointer;
}

.modal__content {
  position: relative;
  width: 920px;
  padding: 60px;

  background-color: $color-vis-vis-7;
  backdrop-filter: blur(17px);
  border-radius: 20px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @include vp-767 {
    width: 100%;
  }

  &--signup {
    padding: 0 190px;
    min-height: 700px;

    @include vp-767 {
      padding: 36px 8px 20px;
      min-height: auto;
    }

    h3 {
      @include font-settings ($ff-inter, 700, 28px, 1);
      text-align: center;
      margin-bottom: 31px;

      @include vp-767 {
        font-size: 21px;
        margin-bottom: 27px;
      }
    }
  }

  &--success {
    padding: 256px 190px 36px;
    text-align: center;

    @include vp-767 {
      padding: 140px 8px 48px;
    }

    h3 {
      @include font-settings ($ff-inter, 700, 44px, 1);
      margin-bottom: 130px;
      padding: 0 20px;

      @include vp-767 {
        font-size: 34px;
        margin-bottom: 101px;
      }
    }

    p {
      @include font-settings ($ff-inter, 500, 14px, 1.1);
      margin-bottom: 18px;

      @include vp-767 {
        font-size: 12px;
        margin-bottom: 17px;
      }
    }
  }

  &--feedback {
    @include vp-767 {
      padding: 36px 8px 20px;
    }

    h3 {
      @include font-settings ($ff-inter, 700, 44px, 1.1);
      margin-bottom: 8px;
      padding-top: 10px;
      text-align: center;

      @include vp-767 {
        font-size: 34px;
      }
    }

    p {
      @include font-settings ($ff-inter, 500, 18px, 1.1);
      color: $color-storm-grey;
      margin-bottom: 30px;

      @include vp-767 {
        font-size: 12px;
      }
    }

    .modal__submit-btn {
      margin-bottom: 20px;
    }
  }
}

.modal__close-btn {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 38px;
  height: 38px;
  padding: 0;
  background: $color-bronze-olive-20;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  z-index: 1;

  svg {
    display: block;
    width: 16px;
    height: 16px;
    margin: 0 auto;
  }

  @include vp-767 {
    width: 29px;
    height: 29px;
    top: 10px;
    right: 10px;

    svg {
      width: 12px;
      height: 12px;
    }
  }
}

.modal form {
  display: flex;
  flex-direction: column;
  align-items: center;

  input {
    @include font-settings ($ff-inter, 400, 16px, 1.5);
    min-width: 288px;
    background: $color-maire2-40;
    border: 1px solid $color-storm-grey;
    border-radius: 12px;
    margin-bottom: 8px;
    padding: 22px 10px 10px;

    @include vp-767 {
      min-width: 273px;
    }
  }

  .inputs-wrapper {
    position: relative;
  }

  label {
    @include font-settings ($ff-inter, 400, 16px, 1.5);
    color: $color-grey-chateau;
    position: absolute;
    top: 16px;
    left: 50%;
    transform: translateX(-50%);
    transition: 0.3s ease all;
    user-select: none;
    cursor: text;

    &[for="comp-name"],
    &[for="comp-username"] {
      left: 40%;
      transform: translateX(-32%);
    }

    &[for="comp-question"],
    &[for="pers-question"] {
      top: 6px;
      left: 29%;
      transform: translateX(-22%);
      text-align: center;
    }
  }

  textarea:focus+label,
  input:focus+label {
    top: 7px;
    left: 10px;
    font-size: 12px;
    transform: none;
  }

  textarea {
    @include font-settings ($ff-inter, 400, 16px, 1.5);
    min-width: 288px;
    min-height: 167px;
    background: $color-maire2-40;
    border: 1px solid $color-storm-grey;
    border-radius: 12px;
    margin-bottom: 24px;
    padding: 16px;

    &:hover {
      border: 1px solid $color-accent;
    }

    @include vp-767 {
      min-width: 273px;
    }
  }

  span {
    @include font-settings ($ff-inter, 400, 12px, 1);
    color: $color-link-water;
    align-self: flex-start;
    padding-left: 10px;
    margin-bottom: 8px;
  }
}

.modal__submit-btn {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.modal__socials {
  .socials__item {
    width: 44px;
    height: 44px;

    svg {
      display: block;
      width: 24px;
      height: 24px;
    }
  }
}
