.events-card {
  position: relative;
  padding: 20px;
  background-color: #1a1d24;
  border: 1px solid #2d3341;
  border-radius: 10px;
  overflow: hidden;

  @include vp-1023 {
    padding: 0 0 20px;
  }
}

.events-card__content {
  display: block;
}

.events-card__cover {
  margin-bottom: 29px;
  height: 300px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

  @include vp-767 {
    height: 30vw;
    min-height: 165px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.events-card__info {
  padding: 0 20px;
  margin-bottom: 40px;
}

.events-card__info-row {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 12px;

  p {
    @include font-settings ($ff-inter, 600, 18px, 1.6);
  }
}

.events-card__text-heading,
.events-card__tags,
.events-card__link {
  margin-bottom: 10px;
}

.events-card__text-heading h2 {
  @include font-settings ($ff-inter, 700, 35px, 1.1);
  letter-spacing: 0.97px;
  text-transform: uppercase;
  margin-right: 15px;

  @include vp-1023 {
    font-size: 24px;
  }
}

.events-card__tags-list {
  display: flex;
  flex-wrap: wrap;
}

.events-card__tags-item {
  margin-right: 10px;
  border: 1px solid #474C53;
  border-radius: 10px;
  padding: 5px 10px;

  span {
    @include font-settings ($ff-inter, 400, 14px, 1);
  }

  @include vp-767 {
    margin-bottom: 10px;
  }
}

.events-card__link {
  margin-left: auto;

  @include vp-767 {
    margin-left: 0;
  }
}

.events-card__link a {
  @include font-settings ($ff-inter, 500, 14px, 1.5);
  position: relative;
  display: inline-block;
  background-color: #262931;
  border-radius: 10px;
  padding: 7px 17px 9px 35px;
  will-change: opacity;
  transition: opacity $trans-default;

  &::before {
    position: absolute;
    content: url("../img/svg/globe.svg");
    top: 10px;
    left: 12px;
    width: 14px;
    height: 14px;
  }

  @include hover {
    opacity: 0.7;
    will-change: opacity;
    transition: opacity $trans-default;
  }
}

.events-card__datetime {
  margin-right: 64px;

  @include vp-1023 {
    margin-right: 30px;
  }
}

.events-card__related {
  display: flex;
  align-items: center;
  margin-top: 20px;

  p {
    font-weight: 400;
    margin-right: 20px;
  }

  .events-card__tags-item {
    background: #262931;
    border-radius: 10px;
    border: none;
    padding: 11px 13px;
  }

  span {
    @include font-settings ($ff-inter, 500, 14px, 1.5);
    border: none;
  }

  @include vp-1023 {
    flex-direction: column;
    align-items: flex-start;
  }
}

.events-card__description {
  padding: 0 20px;
  margin-bottom: 40px;

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    color: $color-white-opacity-50;
    margin-bottom: 10px;
  }

  a {
    text-decoration: underline;
    color: $color-white-opacity-50;
  }

  ul {
    margin-bottom: 15px;
  }

  li {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    color: $color-white-opacity-50;
  }

  h3 {
    @include font-settings ($ff-inter, 400, 26px, 1.5);
    margin-bottom: 15px;
  }
}

.events-card__slider {
  position: relative;
  padding: 0 20px;
  margin-bottom: 63px;
}

.events-card__slider-list {
  margin-bottom: 14px;
}

.events-card__slider-item {
  display: none;
}

.events-card__slider-item.active {
  display: block;
  max-height: 500px;
  height: 35vw;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include vp-1023 {
    height: 45vw;
  }
}

.events-card__slider-thumbs-list {
  display: flex;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);

  @include vp-767 {
    display: none;
  }
}

.events-card__slider-thumbs-item {
  opacity: 0.5;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background-color: $color-default-white;
}

.events-card__slider-thumbs-item:not(:last-child) {
  margin-right: 6px;
}

.events-card__slider-thumbs-item.active {
  opacity: 1;
}

.events-card__slider-thumbs-item {
  cursor: pointer;

  &:hover {
    transform: scale(1.1);
  }
}

.events-card__slider-btn {
  position: absolute;
  top: 40%;
  width: 56px;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.4);
  backdrop-filter: blur(17px);
  cursor: pointer;

  &--prev {
    left: 40px;
  }

  &--next {
    right: 40px;
    transform: rotate(180deg);
  }

  &:hover {
    opacity: 0.8;
  }

  @include vp-767 {
    width: 24px;
    height: 100%;
    top: 0;
    border-radius: 0;
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(3px);

    &--prev {
      left: 20px;
    }

    &--next {
      right: 20px;
    }
  }
}

.events-card__description--photo-block {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @include vp-1023 {
    flex-direction: column;
  }

  .events-card__text {
    width: 57%;

    @include vp-1023 {
      width: 100%;
    }
  }
}

.events-card__image {
  width: 460px;
  height: 333px;
  overflow: hidden;

  @include vp-1023 {
    width: 100%;
    height: auto;
  }

  img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
}

.events-card__description--video-block {
  .events-card__text {
    margin-bottom: 40px;
  }
}

.events-card__video-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
}

.events-card__video {
  position: relative;
  width: 50vw;
  max-width: 730px;
  height: 24vw;
  max-height: 350px;
  border-radius: 10px;
  overflow: hidden;

  @include vp-767 {
    width: 100%;
    min-width: 290px;
    min-height: 210px;
  }
}

.events-card__video-link {
  display: block;


  @include vp-767 {
    min-height: 210px;
  }

  img {
    height: 100%;
    object-fit: cover;
  }
}

.events-card__video.is-active .video__btn {
  display: flex;
}

.video__poster {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.events-card__video-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 148px;
  height: 148px;
  border: none;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.01);
  backdrop-filter: blur(11.5px);
  cursor: pointer;

  img {
    margin-left: 20px;
    display: block;
  }

  &:hover {
    transform: translate(-50%, -50%) scale(1.1);
  }

  @include vp-767 {
    transform: translate(-50%, -50%) scale(0.6);

    &:hover {
      transform: translate(-50%, -50%) scale(0.7);
    }
  }
}

.events-card__partners {
  padding: 0 20px;
  margin-bottom: 40px;

  h3 {
    @include font-settings ($ff-inter, 400, 26px, 1.5);
    margin-bottom: 15px;
  }
}

.events-card__partners-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  justify-items: center;
  align-items: center;

  @include vp-1279 {
    grid-template-columns: repeat(3, 1fr);
  }

  @include vp-767 {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
}

.events-card__partners-item {
  width: 170px;
  display: flex;
  align-items: center;
  justify-content: center;

  @include vp-767 {
    width: 140px;
  }

  a {
    display: block;

    img {
      width: 100%;
      height: auto;
      max-height: 85px;
      object-fit: contain;
    }
  }
}

.events-card__reviews {
  padding: 0 20px;

  h3 {
    @include font-settings ($ff-inter, 400, 26px, 1.5);
    margin-bottom: 15px;
  }
}

.events-card__reviews-item {
  width: 100%;
  background: #262931;
  border-radius: 20px;
  padding: 23px;
  margin-bottom: 14px;
}

.events-card__reviews-author {
  display: flex;
  justify-content: space-between;

  h4 {
    @include font-settings ($ff-inter, 600, 18px, 1.6);
    margin-bottom: 18px;
  }
}

.events-card__reviews-text {
  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    color: $color-white-opacity-50 !important;
  }
}

.events-card__reviews-btn {
  @include font-settings ($ff-inter, 500, 14px, 1);
  border: none;
  min-width: 218px;
  background: #262931;
  border-radius: 10px;
  color: $color-default-white;
  padding: 13px 35px;
  cursor: pointer;

  &:hover {
    background-color: #3e4046;
  }

  @include vp-767 {
    width: 100%;
  }
}

.events-card__landing {
  a {
    @include font-settings ($ff-inter, 500, 14px, 1);
    display: block;
    color: $color-default-black;
    background-color: #fccd03;
    width: fit-content;
    padding: 12px 20px;
    border: 0;
    border-radius: 10px;
    will-change: background-color;
    transition: background-color $trans-default;

    @include hover {
      background-color: #977b02;
      will-change: background-color;
      transition: background-color $trans-default;
    }
  }
}

.connect-style {
  @include font-settings ($ff-inter, 500, 14px, 1);
  display: block;
  color: $color-default-black;
  background-color: #fccd03;
  width: fit-content;
  margin-bottom: 10px;
  padding: 12px 20px;
  border: 0;
  border-radius: 10px;
  will-change: background-color;
  transition: background-color $trans-default;

  @include hover {
    background-color: #977b02;
    will-change: background-color;
    transition: background-color $trans-default;
  }
}
