.dubai__swiper1 {
  position: relative;
  width: 53%;
  max-width: 760px;
  margin: 0 auto;
  cursor: grab;

  @include vp-767 {
    width: 100%;
  }
}

.container.swiper-container {
  position: relative;
  padding-left: 140px;

  @include vp-767 {
    padding-left: 20px;
  }

  &::after {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    width: 176px;
    height: 100%;
    opacity: 0.7;
    background: linear-gradient(270deg, rgba(20, 22, 27, 0.96) 24.57%, rgba(20, 22, 27, 0.00) 100%);
    pointer-events: none;
    z-index: 100;

    @include vp-767 {
      width: 155px;
    }
  }
}

.swiper-container.right {
  @media screen and (min-width: 1024px) {
    cursor: url("../img/events/slider-cursor-right.svg"), grab;
  }
}

.swiper-container.left {
  @media screen and (min-width: 1024px) {
    cursor: url("../img/events/slider-cursor-left.svg"), grab;
  }
}

.swiper-slide {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    user-select: none;
  }
}

.dubai__swiper1 {
  @include vp-767 {
    padding-bottom: 76px;
  }

  .swiper-slide {
    height: 34vw;
    max-height: 445px;

    @include vp-767 {
      height: 91vw;
      max-height: 337px;
    }
  }
}

.dubai__swiper2 {
  .swiper-slide {
    height: 40vw;
    max-height: 361px;

    @include vp-767 {
      height: 52vw;
      max-height: 190px;
    }
  }
}

.rally__swiper {
  .swiper-slide {
    height: 40vw;
    max-height: 446px;

    @include vp-767 {
      height: 48vw;
      max-height: 155px;
    }
  }
}

.swiper__button-prev,
.swiper__button-next {
  position: absolute;
  top: 50%;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: url('../img/events/slider-cursor-left.svg') center no-repeat;
  background-size: contain;
  cursor: pointer;
  z-index: 1000;

  @include vp-767 {
    top: auto;
    bottom: -28px;
  }
}

.swiper__button-prev {
  transform: translateY(-50%);
  left: -76px;

  @include vp-767 {
    left: auto;
    right: 79px;
  }
}

.swiper__button-next {
  transform: translateY(-50%) scale(-1, 1);
  right: -76px;

  @include vp-767 {
    right: 15px;
  }
}
