#branding-container {
  position: fixed;
  top: 70px;
  left: 50%;
  transform: translateX(-50%);
  width: 1920px;
  z-index: 10;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &::before,
  &::after {
    position: absolute;
    content: "";
    width: 100px;
    height: 100%;
    top: 0;
  }

  &::before {
    left: -1px;
    background: linear-gradient(270deg, rgba(0, 0, 0, 0) 0%, rgba(20, 22, 27, 1) 100%);
  }

  &::after {
    right: 0;
    background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, rgba(20, 22, 27, 1) 100%);
  }

  &:hover,
  &:focus,
  &:active {
    opacity: 1;
  }
}

#branding-container-top {
  height: 180px;
  background: transparent;
  pointer-events: none;
}

#top-container {
  height: 70px;
  background: transparent;
}

.branding {
  @include vp-1023 {
    display: none !important;
  }
}
