.promo-wc {
  display: flex;
  margin-top: 15px;

  @include vp-1279 {
    flex-direction: column;
  }

  .wc-info__link {
    margin-right: 15px;
    margin-top: 0;
  }
}

.promo__list {
  display: flex;
  align-items: center;

  @include vp-1279 {
    margin-top: 15px;
  }
}

.promo__item {
  width: 80px;
  height: 40px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.35);
  margin-bottom: 7px;
  padding: 6px;

  @include vp-1023 {
    width: 64px;
  }

  a {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }

  &:not(:last-child) {
    margin-right: 10px;
  }

  @include hover {
    opacity: 0.6;
    will-change: opacity;
    transition: opacity $trans-default;
  }
}
