.courses {
  background: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 49px 40px 40px;

  @include vp-767 {
    padding: 20px 10px;
  }
}

.courses__heading {
  h2 {
    @include font-settings ($ff-inter, 700, 35px, 0.99);
    letter-spacing: 0.96px;
    text-transform: uppercase;
    margin-bottom: 41px;

    @include vp-767 {
      font-size: 25px;
      line-height: 1.2;
      margin-bottom: 12px;
      padding-left: 20px;
    }
  }
}

.courses__content {
  display: grid;
  grid-template-columns: auto 360px;
  column-gap: 40px;
  row-gap: 15px;

  @include vp-1279 {
    grid-template-columns: auto 295px;
  }

  @include vp-1023 {
    grid-template-columns: auto;
  }
}

.courses__list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.courses__item {
  min-width: 371px;
  height: 250px;
  display: flex;
  justify-self: center;
  border-radius: 20px;
  overflow: hidden;

  &:hover {
    box-shadow: 0px 0px 20px rgba(10, 57, 184, 0.25);
  }

  &:active {
    opacity: 0.6;
  }

  @include vp-1279 {
    min-width: 269px;
  }
}

.courses__cover {
  position: absolute;
  inset: 0 0 0 0;
  height: 250px;
  border-radius: 20px;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 53%, rgba(0, 0, 0, 0.9) 100%);
  }
}

.courses__card {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: flex-end;
  padding: 17px 12px;

  h3 {
    @include font-settings ($ff-inter, 700, 20px, 1.5);
    position: relative;
    letter-spacing: 0.96px;
    text-transform: uppercase;
    margin-bottom: 5px;
    z-index: 10;
  }

  p {
    @include font-settings ($ff-inter, 500, 16px, 1.2);
    letter-spacing: 0.96px;
    opacity: 0.8;
    z-index: 10;
  }
}
