.allies {
  margin: 0 auto;
  padding: 0 20px 20px;
  border-radius: 10px;
  border: 1px solid #2D3341;
  background: #1A1D24;
  display: flex;
  flex-direction: column;
  align-items: center;

  @include vp-1023 {
    padding: 30px 10px 20px;
  }
}

.allies__intro {
  margin: 0 auto;
  display: flex;

  @include vp-1023 {
    flex-direction: column;
    align-items: center;
  }
}

.allies__intro-image {
  margin-top: -36px;
  margin-right: 35px;
  width: 467px;
  height: 707px;

  @include vp-1023 {
    width: 300px;
    height: auto;
    margin-top: -52px;
    margin-right: 0;
  }
}

.allies__intro-content {
  margin-right: 35px;
  padding-top: 154px;

  @include vp-1023 {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0;
    padding-top: 0;
  }
}

.allies__intro-logo {
  width: 160px;
  height: 28px;
  margin-bottom: 13px;

  @include vp-1023 {
    width: 119px;
    height: 17px;
    margin: 0 auto 15px;
  }
}

.allies__intro-heading {
  width: 369px;
  height: 78px;
  margin-bottom: 30px;

  @include vp-1023 {
    width: 233px;
    height: 49px;
    margin: 0 auto 20px;
  }
}

.allies__intro-image img,
.allies__intro-logo img,
.allies__intro-heading img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.allies__intro-text {
  max-width: 628px;

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin-bottom: 16px;
    color: rgba(255, 255, 255, 0.8);

    &:last-child {
      font-style: italic;
    }

    @include vp-767 {
      font-size: 12px;
    }
  }
}

.allies__list {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  width: 100%;
  margin: 0 auto 114px;

  @include vp-1279 {
    grid-template-columns: repeat(2, 1fr);
    padding-top: 50px;
  }

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.allies__item {
  position: relative;
  width: 359px;
  height: 694px;
  transform: scale(0.9);
  opacity: 0.7;

  @include vp-767 {
    width: 300px;
    height: 580px;
    transform: none;
    opacity: 1;
    margin-bottom: 60px;
  }
}

.allies__item:hover {
  transition: all 1.8s cubic-bezier(.4, 0, 0, 1);
  transform: scale(1);
  opacity: 1;

  @include vp-767 {
    transform: none;
  }
}

.allies__link {
  display: block;
  width: 100%;
  height: 100%;
}

.allies__image-wrapper>* {
  position: absolute;
  width: 100%;
  height: 100%;
  perspective: 150px;
  transform-style: preserve-3d;
}

.allies__image {
  position: absolute;
  width: 100%;
  height: 100%;
}

.allies__image-left {
  perspective-origin: left center;
  clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
}

.allies__image-right {
  perspective-origin: right center;
  clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
  margin-left: -1px;
}

.allies__item .allies__image-left .allies__image {
  transform: rotateY(-1deg) scaleX(0.92);
  transition: all 1.8s ease-in-out;

  @include vp-767 {
    transform: none;
  }
}

.allies__item:hover .allies__image-left .allies__image {
  animation: k-left-side 2s ease-in-out infinite;

  @include vp-767 {
    animation: none;
  }
}

.allies__item .allies__image-right .allies__image {
  transform: rotateY(0deg) scaleX(1);
  transition: all 1.8s ease-in-out;

  @include vp-767 {
    transform: none;
  }
}

.allies__item:hover .allies__image-right .allies__image {
  animation: k-right-side 2s ease-in-out infinite;

  @include vp-767 {
    animation: none;
  }
}

/* ANIMATIONS */

@keyframes k-left-side {
  0% {
    transform: rotateY(-1deg) scaleX(0.92);
  }

  50% {
    transform: rotateY(0deg) scaleX(1);
  }

  100% {
    transform: rotateY(-1deg) scaleX(0.92);
  }
}

@keyframes k-right-side {
  0% {
    transform: rotateY(0deg) scaleX(1);
  }

  50% {
    transform: rotateY(1deg) scaleX(0.92);
  }

  100% {
    transform: rotateY(0deg) scaleX(1);
  }
}
