.company-card {
  margin: 30px 0;
  padding: 17px 20px;
  background-color: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;

  @include vp-1023 {
    margin-top: 18px;
    padding: 0;
    background-color: transparent;
    border: none;
    gap: 0;
  }
}

.company-card__content {
  display: block;
  margin-bottom: 20px;
}

.company-card__image-wrapper {
  position: relative;
  width: 100%;
  margin-bottom: 32px;

  @include vp-1023 {
    margin-bottom: 16px;
  }
}

.company-card__background {
  width: 100%;
  border-radius: 10px;
  overflow: hidden;

  @include vp-1023 {
    height: 45vw;
    border-radius: 4px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.company-card__logo {
  position: absolute;
  max-width: 380px;
  height: 122px;
  bottom: 20px;
  left: 20px;
  background: rgba(255, 255, 255, 0.35);
  backdrop-filter: blur(5px);
  border-radius: 10px;
  padding: 20px 30px;

  @include vp-1023 {
    max-width: 150px;
    height: 48px;
    padding: 7px 12px;
    border-radius: 4px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.company-card__heading {
  margin-bottom: 27px;
  padding: 5px 0 0 20px;

  @include vp-1023 {
    margin-bottom: 18px;
    padding-left: 15px;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 35px, 1);
    letter-spacing: 0.965862px;
    text-transform: uppercase;

    @include vp-1023 {
      font-size: 20px;
      line-height: 1.74;
    }
  }
}

.company-card__text {
  padding: 0 20px;

  @include vp-1023 {
    background-color: #1A1D24;
    border: 1px solid #2D3341;
    border-radius: 10px;
    padding: 20px;
    order: 1;
  }

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.57);
    margin-bottom: 22px;
  }

  h3 {
    @include font-settings ($ff-inter, 700, 18px, 1.25);
    margin: 12px 0;
    color: $color-accent;
  }

  h4 {
    @include font-settings ($ff-inter, 700, 16px, 1.25);
    margin: 12px 0;
    color: $color-accent;
  }

  img {
    width: 100%;
    height: auto;
    object-fit: cover;
    margin-bottom: 10px;
  }

  a {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    font-style: normal;
    color: $color-accent;
    text-decoration: underline;
    margin-bottom: 12px;
  }

  ul,
  ol {
    padding-left: 24px;
    margin: 10px 0 16px;
  }

  li {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin-bottom: 6px;
  }

  li {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin-bottom: 6px;
  }

  code {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  pre {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    white-space: pre-wrap;
    word-wrap: break-word;
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  blockquote {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin: 10px 0;
    padding-left: 16px;
    border-left: 2px solid #DEE2E6;
  }

  hr {
    height: 1px;
    margin: 12px 0;
    background-color: #DEE2E6;
    border: none;
  }
}

.company-card__info {
  display: flex;
  flex-direction: column;
  padding-right: 23px;

  h3 {
    @include font-settings ($ff-inter, 600, 18px, 1.56);
    margin-bottom: 9px;
  }

  @include vp-1023 {
    align-items: center;
    background-color: #1A1D24;
    border: 1px solid #2D3341;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 27px;
  }
}

.company-card__btns {
  margin-left: auto;
  display: flex;
  margin-bottom: 21px;

  @include vp-1023 {
    margin-left: 0;
    margin-bottom: 18px;
  }
}

.company-card__edit-btn {
  width: 47px;
  height: 47px;
  background-color: #262931;
  border: none;
  border-radius: 6px;
  color: $color-default-white;
  margin-right: 11px;
  cursor: pointer;

  &:hover {
    background-color: #76777C;
  }

  &:active {
    opacity: 0.6;
  }
}

.company-card__more-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $color-accent;
  border-radius: 10px;
  color: #14161B;
  padding: 10px 16px;

  &:hover {
    background-color: $color-accent-hover;
  }

  &:active {
    opacity: 0.6;
  }

  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
    color: #14161b;
    margin-right: 5px;
  }
}

.company-card__stats-block {
  background-color: #262931;
  border-radius: 20px;
  padding: 19px 16px 16px;
  margin-bottom: 16px;

  @include vp-1023 {
    width: 100%;
    padding: 19px 19px 17px 21px;
    margin-bottom: 20px;
  }
}

.company-card__stats-list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  padding-right: 29px;

  @include vp-767 {
    padding: 0;
  }
}

.company-card__stats-item {
  display: flex;
  align-items: center;
  margin-bottom: 5px;

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.1);
    opacity: 0.5;
    margin-right: 25px;

    @include vp-767 {
      margin-right: 7px;
    }
  }

  span {
    @include font-settings ($ff-inter, 600, 14px, 2);
    margin-right: 20px;
    letter-spacing: 0.3px;

    @include vp-767 {
      margin: 0;
    }
  }
}

.company-card__contacts-block {
  background-color: #262931;
  border-radius: 20px;
  padding: 19px;
  padding-right: 9px;

  @include vp-1023 {
    width: 100%;
  }

  h3 {
    margin-bottom: 10px;

    @include vp-767 {
      margin-bottom: 8px;
    }
  }
}

.company-card__contacts-list {
  display: flex;
  margin-bottom: 3px;
  flex-wrap: wrap;
}

.company-card__contacts-item {
  width: 37px;
  height: 37px;
  background: #1E2228;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 10px 9px 0;

  a {
    display: flex;
  }

  &:hover {
    background-color: #76777C;
  }

  &:active {
    opacity: 0.6;
  }
}
