#modal-connect {
  .modal__wrapper {
    @include vp-1023 {
      padding: 12px;
      overflow-x: hidden;
    }
  }

  .modal__close-btn {
    width: 49px;
    height: 49px;
    background-color: rgba(255, 255, 255, 0.1);

    @include vp-1023 {
      width: 29px;
      height: 29px;
      background-color: rgba(86, 72, 0, 0.2);
      top: 6px;
      right: 6px;
    }
  }
}

.connect {
  position: relative;
  width: 960px;
  height: 680px;
  background: url("../img/connect/connect-bg.png") no-repeat;
  background-size: cover;
  border-radius: 12px;
  border: 2px solid #ffcc48;
  align-items: center;
  justify-content: center;
  padding-right: 25px;

  @include vp-1023 {
    height: 607px;
    padding: 12px;
    border-radius: 20px;
  }
}

.connect__wrapper {
  display: grid;
  grid-template-columns: 179px 296px 10px 390px;
  grid-template-rows: repeat(2, auto);

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.connect__content {
  grid-column: 1 / 4;
  grid-row: 1 / 2;
  align-self: flex-end;

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.connect__logo {
  width: 255px;
  height: 39px;
  margin-bottom: 23px;

  @include vp-1023 {
    width: 162px;
    height: 25px;
    margin-bottom: 2px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.connect__image {
  grid-column: 3 / 5;
  grid-row: 1 / 3;
  position: relative;
  width: 400px;
  height: 508px;
  margin-top: 13px;

  @include vp-1023 {
    width: 264px;
    height: 262px;
    border-radius: 32px;
    overflow: hidden;
    margin-top: 0;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  p {
    @include font-settings ($ff-inter, 500, 14px, 1.1);
    position: absolute;
    bottom: 20px;
    left: 20px;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(4px);
    padding: 12px;

    @include vp-1023 {
      font-size: 12px;
      left: 50%;
      transform: translateX(-50%);
      min-width: 206px;
      bottom: 14px;
      text-align: center;
    }
  }
}

.connect__title {
  align-items: flex-start;
  margin-bottom: 15px;

  .connect__title-img {
    position: relative;
    margin-bottom: 15px;

    @include vp-1023 {
      width: 239px;
      height: 26px;
      margin-right: 30px;
      margin-bottom: 9px;
    }

    img {
      width: 100%;
      height: auto;
      object-fit: contain;
      position: relative;
      z-index: 2;
    }

    &::before {
      @include font-settings ($ff-ttt, 800, 28px, 1);
      position: absolute;
      content: "2.0";
      color: $color-default-black;
      bottom: -6px;
      right: -70px;
      border-radius: 75px;
      background: #ffcc48;
      padding: 9px 19px 5px;
      transform: rotate(5deg);
      z-index: 10;

      @include vp-1023 {
        font-size: 14px;
        bottom: -4px;
        right: -34px;
        padding: 5px 9px 2px;
      }
    }

    &::after {
      position: absolute;
      content: "";
      bottom: -11px;
      right: -54px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background-color: #ffdc44;
      filter: blur(9px);
      z-index: 1;

      @include vp-1023 {
        bottom: -8px;
        right: -29px;
        width: 30px;
        height: 30px;
        filter: blur(5px);
      }
    }
  }

  p {
    @include font-settings ($ff-ttt, 800, 26px, 1);
    letter-spacing: 0.3px;
    text-transform: uppercase;

    @include vp-1023 {
      font-size: 13px;
      font-weight: 700;
      letter-spacing: 0.25px;
      text-align: center;
    }
  }
}

.connect__sub {
  display: flex;
  align-items: center;
  margin-bottom: 35px;

  span {
    @include font-settings ($ff-inter, 400, 12px, 1.2);
    margin-right: 10px;

    @include vp-1023 {
      font-size: 8px;
      margin-right: 0;
    }
  }

  a {
    display: block;
    width: 79px;
    height: 17px;

    @include vp-1023 {
      width: 61px;
      height: 13px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}

.connect__link {
  grid-column: 1 / 2;
  grid-row: 2 / 3;

  a {
    @include font-settings ($ff-inter, 600, 18px, 1.42);
    color: #191500;
    letter-spacing: 0.13px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: 182px;
    height: 51px;
    padding: 12px;
    border-radius: 4px;
    background: #ffc42c;

    @include vp-1023 {
      min-width: 265px;
      height: 44px;
      margin-top: 37px;
      font-size: 14px;
    }
  }
}

.connect__info {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  padding-top: 9px;
  padding-left: 21px;

  @include vp-1023 {
    order: -1;
    margin-bottom: 34px;
    padding-top: 5px;
    padding-left: 0;
  }

  p {
    @include font-settings ($ff-inter, 500, 15px, 1.2);
    letter-spacing: 0.8px;
    text-transform: uppercase;

    @include vp-1023 {
      font-size: 14px;
      text-align: center;
    }
  }
}

.connect__decors {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.connect__decor {
  position: absolute;
  z-index: 10;

  &--1 {
    top: -32px;
    left: -103px;

    @include vp-1023 {
      display: none;
    }
  }

  &--2 {
    top: -23px;
    right: 391px;

    @include vp-1023 {
      transform: scale(0.6);
      top: -77px;
      right: -16px;
    }
  }

  &--3 {
    top: 79px;
    right: -60px;

    @include vp-1023 {
      transform: scale(0.6);
      top: 169px;
      right: -90px;
    }
  }

  &--4 {
    top: 282px;
    right: 18px;

    @include vp-1023 {
      top: 424px;
      right: -40px;
    }
  }

  &--5 {
    bottom: -53px;
    left: -38px;

    @include vp-1023 {
      bottom: 82px;
      left: -90px;
    }
  }

  &--6 {
    bottom: 48px;
    left: 155px;

    @include vp-1023 {
      bottom: 483px;
      left: -19px;
      transform: scale(0.7);
    }
  }

  &--7 {
    bottom: -71px;
    right: -34px;

    @include vp-1023 {
      display: none;
    }
  }
}

.tooltip__text {
  display: none;
  position: absolute;
  bottom: 147px;
  right: 73px;
  width: 330px;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(2px);
  border: 1px solid $color-accent;
  border-radius: 10px;
  padding: 12px 18px;
  z-index: 30;

  @include vp-1023 {
    width: 283px;
    left: 50%;
    transform: translateX(-50%);
    bottom: 171px;
    padding: 13px 9px 9px 15px;
  }

  span {
    @include font-settings ($ff-inter, 500, 13px, 1.2);
    display: inline-block;
    letter-spacing: 0.39px;

    @include vp-1023 {
      font-size: 12px;
      letter-spacing: normal;
    }
  }
}

.tooltip__btn {
  @include font-settings ($ff-inter, 400, 12px, 1);
  position: absolute;
  bottom: 122px;
  right: 162px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  border: 1px solid $color-default-white;
  background-color: rgba(0, 0, 0, 0.8);
  cursor: pointer;
  padding: 0;
  z-index: 30;

  @include vp-1023 {
    bottom: 142px;
    right: 50%;
    transform: translateX(115px);
  }
}

.tooltip__btn:hover {
  @media screen and (min-width: 1023px) {
    border-color: $color-accent;
    color: $color-accent;
  }
}

.tooltip__btn.active {
  @include vp-1023 {
    border-color: $color-accent;
    color: $color-accent;
  }
}

.tooltip__btn:hover~.tooltip__text {
  @media screen and (min-width: 1023px) {
    display: block;
  }
}

.tooltip__btn.active~.tooltip__text {
  @include vp-1023 {
    display: block;
  }
}

.tooltip__btn-x {
  display: none;
  font-size: 20px;
  transform: translate(1px, -1px) rotate(45deg);
  color: $color-accent;
}

.tooltip__btn.active {
  .tooltip__btn-i {
    @include vp-1023 {
      display: none;
    }
  }

  .tooltip__btn-x {
    @include vp-1023 {
      display: block;
    }
  }
}
