.adblock {
  border-radius: 20px;
  overflow: hidden;
  margin: 0 auto;
}

// под хедером на всех страницах кроме главной
.adblock-1240x110 {
  max-width: 1240px;
  max-height: 110px;
  margin: 20px auto 20px;
  height: auto;

  @include vp-767 {
    display: none;
  }

  img {
    width: 100%;
    height: auto;
  }
}

// верхний большой на главной под интро
.adblock-1110x105 {
  max-width: 1110px;
  max-height: 105px;
  width: 100%;
  height: auto;

  img {
    width: 100%;
    height: auto;
  }
}

// 4 маленьких на главной
.adblock-540x105 {
  max-width: 540px;
  max-height: 105px;
  margin-bottom: 30px;
  width: 100%;
  height: auto;

  img {
    width: 100%;
    height: auto;
  }
}

// на главной форума между блоками и в блоках наверху
.adblock-925x110 {
  width: 925px;
  height: 110px;
  margin-bottom: 28px;

  &--index {
    margin-bottom: 21px;
  }

  @include vp-1279 {
    display: none;
  }
}

// в блоге и статье между карточками постов и текстом
.adblock-760x110 {
  width: 760px;
  height: 110px;
  margin-bottom: 20px;

  @include vp-1279 {
    display: none;
  }
}

// в сайдбаре каталога, ивентов, блога, поста, курсов десктоп
.adblock-360x430 {
  max-width: 360px;
  max-height: 430px;
  height: auto;
  margin-bottom: 20px;

  @include vp-767 {
    max-width: 295px;
  }

  img {
    width: 100%;
    height: auto;
  }
}

// в сайдбаре каталога, ивентов, блога, поста, курсов мобилка + сайдбар форума
.adblock-295x430 {
  width: 295px;
  height: 430px;

  @include vp-1279 {
    display: none;
  }
}

// в меню хедера в десктопной версии
.adblock-220x320 {
  width: 220px;
  height: 320px;
  border-radius: 15px;
  overflow: hidden;

  @include vp-767 {
    display: none;
  }
}

// в меню хедера в мобильной версии + на всех страницах кроме главной верхний баннер под хедером
.adblock-290x76 {
  display: none;

  @include vp-767 {
    display: block;
    margin: 0 auto 20px;
    border-radius: 12px;
  }
}

.sidebar {
  .adblock-295x430 {
    display: none;
  }
}

.adblock-container.two-blocks {
  .adblock-540x105:first-child {
    margin-right: 14px;

    @include vp-1279 {
      margin: 0 auto;
    }
  }

  .adblock-540x105:nth-child(2) {
    margin-left: 14px;

    @include vp-1279 {
      margin: 0 auto;
    }
  }
}
