.allstars {
  background: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 53px 40px 73px;

  @include vp-1023 {
    padding: 30px 15px;
  }
}

.allstars__description {
  margin-bottom: 5px;

  h2 {
    @include font-settings ($ff-ttt, 800, 55px, 1);
    text-transform: uppercase;
    letter-spacing: 1.1px;
    text-align: center;
    margin-bottom: 30px;

    @include vp-1023 {
      font-size: 30px;
    }
  }

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    max-width: 55%;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 20px;
    opacity: 0.8;

    @include vp-1023 {
      max-width: 100%;
    }
  }

  .bordered {
    border-top: 1px solid rgba(255, 255, 255, 0.8);
    padding-top: 15px;
  }
}

.allstars__cards-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.allstars__main-container {
  height: 570px;
  width: 347px;
  background-color: transparent;
  perspective: 1000px;
}

.allstars__card-container {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform .8s;
  transform-style: preserve-3d;
}

.allstars__main-container:hover .allstars__card-container {
  @media screen and (min-width: 1024px) {
    transform: rotateY(180deg);
  }
}

.allstars__main-container.active .allstars__card-container {
  @include vp-1023 {
    transform: rotateY(180deg);
  }
}

.allstars__front-card,
.allstars__back-card {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.allstars__front-card {
  background: url("../img/allstars/front-1.png") center no-repeat;
  background-size: 100%;

  .allstars__card-image {
    position: absolute;
    top: 60px;
    right: 15px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .allstars__card-icons {
    position: absolute;
    top: 69px;
    left: 50px;
  }

  .allstars__icons__list {
    height: 210px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .allstars__card-info {
    position: relative;
    margin-top: 300px;

    .allstars__name {
      @include font-settings ($ff-inter, 700, 25px, 1);
      text-transform: uppercase;
      color: $color-default-black;
      border-bottom: 1px solid $color-default-black;
      max-width: 80%;
      margin: 0 auto;
      margin-bottom: 7px;
      padding-bottom: 7px;
    }
  }

  .allstars__front-image {
    position: absolute;
    top: 0;
  }

  .allstars__inner-item {
    margin-bottom: 10px;

    &--tight {
      margin-bottom: 5px;
    }
  }
}

.allstars__back-card {
  background: url("../img/allstars/back.png") center no-repeat;
  background-size: 100%;
  transform: rotateY(180deg);

  .allstars__inner-content {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  .allstars__inner-item {
    margin-bottom: 30px;
  }
}

.allstars__inner-list {
  list-style: disc;
  margin: 0 15px;
  padding: 0 19px 0 40px;
}

.allstars__inner-item {
  @include font-settings ($ff-inter, 500, 14px, 1.2);
  color: $color-default-black;
  text-align: left;
}

.allstars__icons__item {
  position: relative;
  width: 57px;
  height: 57px;
  border-radius: 50%;
  background: linear-gradient(180deg, #FFF1C0 0%, #BC9400 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 13px;

  .allstars__circle-inner {
    content: "";
    width: 51px;
    height: 51px;
    border-radius: 50%;
    background: linear-gradient(180deg, #F3D28C 0%, #DDAF41 100%);
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.allstars__contacts-wrapper {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100;
}

.allstars__contacts-list {
  display: flex;
  justify-content: center;
}

.allstars__contacts-item {
  a {
    opacity: 0.4;
  }
}

.allstars__contacts-item {
  a[href] {
    opacity: 1;

    &:hover {
      opacity: 0.8;
    }
  }
}

.allstars__contacts-item:not(:last-child) {
  margin-right: 10px;
}
