.wc {
  padding-bottom: 57px;

  @include vp-767 {
    margin-bottom: 21px;
    padding-bottom: 0;
  }

  b {
    color: $color-accent;
  }
}

.wc-intro {
  position: relative;
  display: flex;
  background: url("../img/backgrounds/top-bg-blur.png") center 100% no-repeat;

  @include vp-767 {
    background: url("../img/backgrounds/top-bg-blur.png") center 80% no-repeat;
  }
}

.wc-intro div.wc-intro-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 150px;
  padding-bottom: 21px;

  @include vp-767 {
    padding-top: 136px;
    padding-bottom: 40px;
  }
}

.wc-intro__heading {
  text-align: center;

  p {
    @include font-settings ($ff-ttt, 700, 24px, 1);
    color: $color-accent;
    text-transform: uppercase;
    margin-bottom: 20px;

    @include vp-767 {
      font-size: 16px;
      margin-bottom: 16px;
    }
  }

  h2 {
    @include font-settings ($ff-ttt, 700, 50px, 1);
    text-transform: uppercase;
    margin-bottom: 66px;
  }
}

.wc-intro__logo {
  width: 424px;
  height: 65px;
  margin: 0 auto;
  margin-bottom: 65px;

  @include vp-767 {
    width: 278px;
    height: 41px;
    margin-bottom: 33px;
  }

  img {
    width: 100%;
    height: auto;
    object-fit: contain;
  }
}

.wc-intro__content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 224px;

  @include vp-767 {
    margin-bottom: 92px;
  }
}

.wc-intro__circle {
  border-radius: 50%;
  background-color: $color-golden-puppy-7;
  display: flex;
  justify-content: center;
  align-items: center;

  img {
    object-fit: contain;

    @include vp-767 {
      transform: scale(0.75);
    }
  }

  &--main {
    width: 106px;
    height: 106px;
    background: radial-gradient(88.73% 88.73% at 50.29% 11.27%, $color-sweet-corn 0%, $color-accent 100%);
    margin: 0 48px;

    @include vp-767 {
      width: 42px;
      height: 42px;
      margin: 0 16px;
    }
  }

  &--l {
    width: 90px;
    height: 90px;
    margin: 0 21px;

    @include vp-767 {
      width: 35px;
      height: 35px;
      margin: 0 10px;
    }
  }

  &--m {
    width: 66px;
    height: 66px;

    @include vp-767 {
      width: 26px;
      height: 26px;
    }
  }

  &--s {
    width: 55px;
    height: 55px;
    margin: 0 32px;

    @include vp-767 {
      width: 22px;
      height: 22px;
      margin: 0 10px;
    }
  }
}

.wc-intro__decor {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 50%;
  background: radial-gradient(88.73% 88.73% at 50.29% 11.27%, rgba(254, 230, 126, 0.4) 0%, rgba(252, 205, 3, 0.4) 100%);
  opacity: 0.1;
  backdrop-filter: blur(13.5);
  z-index: -1;

  &--1 {
    width: 898px;
    height: 898px;
    top: -72px;

    @include vp-767 {
      width: 374px;
      height: 374px;
      top: 76px;
    }
  }

  &--2 {
    width: 723px;
    height: 723px;
    top: 15px;

    @include vp-767 {
      width: 282px;
      height: 282px;
      top: 123px;
    }
  }

  &--3 {
    width: 538px;
    height: 538px;
    top: 105px;

    @include vp-767 {
      width: 210px;
      height: 210px;
      top: 160px;
    }
  }

  &--4 {
    width: 333px;
    height: 333px;
    top: 210px;

    @include vp-767 {
      width: 130px;
      height: 130px;
      top: 199px;
    }
  }

  &--5 {
    width: 185px;
    height: 185px;
    top: 285px;

    @include vp-767 {
      width: 72px;
      height: 72px;
      top: 227px;
    }
  }
}

.wc-intro__down {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  margin: 0 auto 173px;
  z-index: 100;

  @include vp-767 {
    margin-bottom: 75px;
  }

  svg {
    transform: rotate(180deg);

    @include vp-767 {
      transform: rotate(180deg) scale(0.65);
    }
  }
}

