    padding-bottom: 170px;

    @include vp-767 {
      padding-top: 0;
      padding-bottom: 341px;
    }
  }

  h2 {
    padding-left: 67px;

    @include vp-1023 {
      padding-left: 0;
    }
  }

  .wc-description__image {
    &--1 {
      width: 461px;
      height: 296px;
      top: 39px;
      right: 70px;

      @include vp-1279 {
        width: 266px;
        height: auto;
      }

      @include vp-767 {
        top: 169px;
        left: 23px;
      }
    }

    &--2 {
      width: 401px;
      height: 324px;
      top: 136px;
      right: 78px;

      @include vp-1279 {
        width: 233px;
        height: auto;
        top: 66px;
      }

      @include vp-767 {
