    padding-bottom: 170px;

    @include vp-767 {
      padding-top: 29px;
      padding-bottom: 330px;
    }
  }

  .wc-description__image {
    &--1 {
      width: 527px;
      height: 526px;
      top: -23px;
      left: -23px;
      z-index: -1;

      @include vp-1279 {
        width: 311px;
        height: auto;
        top: 46px;
        left: 17px;
      }

      @include vp-767 {
        top: 111px;
        left: -18px;
      }
    }

    &--2 {
      width: 289px;
      height: 294px;
      top: 194px;
      left: 239px;
      z-index: -2;

      @include vp-1279 {
        width: 169px;
        height: auto;
        top: 164px;
        left: 159px;
      }

      @include vp-767 {
        top: 238px;
        left: 136px;
      }
