  }
}

.wc-signup {
  p {
    @include font-settings ($ff-inter, 500, 14px, 1.1);

    @include vp-767 {
      font-size: 14px;
      line-height: 1.2;
      padding: 0 40px;
    }
  }

  .wc-signup__heading {
    margin-right: 105px;
    padding-top: 5px;

    @include vp-1279 {
      margin-right: 0;
      margin-bottom: 30px;
    }
  }
}

.wc-signup__wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 67px 160px;
  padding: 31px 51px;
  background: rgba(255, 229, 138, 0.07);
  backdrop-filter: blur(17px);
  border-radius: 20px;
  border: 1px solid $color-accent-18;

  @include vp-1279 {
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 0;
    margin-bottom: 50px;
  }

  @include vp-767 {
    padding: 19px 15px;
  }

  &::after {
    position: absolute;
    content: url("../img/svg/signup-arrow.svg");
    bottom: -17px;
    left: 60%;

    @include vp-1279 {
      display: none;
    }
  }
}

.wc-signup__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 57px;
  height: 57px;
  background: $color-black-40;
  border-radius: 5px;

  @include vp-767 {
    display: none;
  }
}

.wc-signup__heading {
  margin-right: 24px;

  @include vp-767 {
    margin-bottom: 22px;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 36px, 1);
    margin-bottom: 9px;

    @include vp-767 {
      font-size: 24px;
      margin-bottom: 4px;
    }
  }
}

.wc-signup-bottom {
  .wc-signup__wrapper {
    margin-top: 86px;
    margin-bottom: 0;

    @include vp-767 {
      margin-top: 30px;
    }
  }
}

.wc-btn {
  @include font-settings ($ff-inter, 500, 19px, 1.43);
  color: $color-maire2;
  background-color: $color-accent;
  display: inline-flex;
  letter-spacing: 0.14px;
  padding: 14px 33px;
  border: none;
  border-radius: 14px;
  will-change: background-color;
  transition: background-color $trans-default;

  &:hover {
    background-color: $color-olive;
  }
}

.wc-info {
  background:
    url("../img/backgrounds/info-bg-blur-1.png") 50% 20% no-repeat,
    url("../img/backgrounds/info-bg-blur-2.png") 30% 120% no-repeat;

  @include vp-767 {
    background:
      url("../img/backgrounds/info-bg-blur-1.png") center 26% no-repeat,
