.wc-partners {
  .container {
    padding-top: 68px;
    padding-bottom: 50px;
    display: flex;
    flex-direction: column;

    @include vp-767 {
      padding-top: 55px;
      padding-bottom: 20px;
    }
  }

  h2 {
    @include font-settings ($ff-ttt, 700, 36px, 1);
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 33px;

    @include vp-767 {
      font-size: 24px;
      margin-bottom: 38px;
    }
  }
}

.wc-partners__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: -15px;
  margin-right: -15px;
}

.wc-partners__item {
  margin: 0 15px 20px;

  @include vp-767 {
    margin-bottom: 27px;
  }
}

.wc-partners__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 160px;
  will-change: transform;
  transition: transform 0.3s ease-in;

  @include vp-767 {
    width: 130px;
  }

  img {
    width: 100%;
    object-fit: contain;
  }

  &:hover {
