.wc-contacts {
  text-align: center;

  .container {
    padding-top: 123px;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 44px, 1.1);
    margin-bottom: 14px;

    @include vp-767 {
      font-size: 24px;
      margin-bottom: 12px;
    }
  }

  p {
    @include font-settings ($ff-inter, 500, 24px, 1.1);
    color: $color-storm-grey;
    margin-bottom: 37px;

    @include vp-767 {
      font-size: 16px;
      margin-bottom: 14px;
    }
  }

  .wc-contacts__btn {
    @include font-settings ($ff-inter, 500, 14px, 1.43);
    letter-spacing: 0.1px;
    padding: 10px 24px;
    background-color: transparent;
    border: 1px solid $color-storm-grey;
    border-radius: 10px;
    margin-bottom: 92px;
    cursor: pointer;
    will-change: background-color;
    transition: all 0.3s ease-in;

    @include vp-767 {
      margin-bottom: 81px;
    }

    &:hover {
      background-color: rgba(255, 255, 255, 0.06);
      border: 1px solid $color-default-white;
    }
