.wc-socials__list {
  display: flex;
  flex-wrap: wrap;

  @include vp-767 {
    margin-right: -10px;
  }
}

.wc-socials__item {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 33px;
  height: 33px;
  border-radius: 11px;
  background: $color-black-russian;
  backdrop-filter: blur(4.5px);
  margin: 0 8px 10px 0;

  &:hover {
    background-color: $color-black-russian-50;
  }

  svg {
    display: block;
    width: 20px;
    height: 20px;
  }

  @include vp-767 {
    width: 49px;
    height: 49px;
    margin: 0 10px 10px 0;

    svg {
      width: 24px;
      height: 24px;
    }
  }
}

.wc-socials__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.header-nav__heading--wc {
  display: none;
}

.wc-style.wrapper {

  .branding {
    display: none !important;
  }

  .header {
    background-color: transparent;
    position: fixed;
    background: rgba(255, 255, 255, .01);
    backdrop-filter: blur(7px);

    @include vp-767 {
      position: relative;
      background: transparent;
      backdrop-filter: blur(0);
    }
  }

  .header__mobile-links {
    display: none;
  }

  .header__logo {
    @include vp-1023 {
      width: 127px;
      height: 20px;
    }

    span {
      display: none;
    }
  }

  .header__search {
    display: none;
  }

  #top-container {
    display: none;
  }

  .header__divider {
    display: none;
  }

  .header__forum-link {
    display: none;
  }

  .header__user-menu {
    width: 71%;

    @include vp-1023 {
      width: auto;
      margin-left: auto;
    }
  }

  .user-menu__alerts {
    display: none;
  }

  .user-menu__lang-switcher-wrapper {
    &:hover {
      background: #3E3613;
      box-shadow: 0 0 1px 1px rgba(255, 255, 255, 0.2);
    }

    @include vp-1023 {
      z-index: 2000;
    }
  }

  .user-menu__user-block {
    @include vp-1023 {
      z-index: 2000;
    }
  }

  .user-menu__nav-btn {
    display: none;

    @include vp-1023 {
      position: relative;
      display: block;
      width: 34px;
      height: 34px;
      background: #282516;
      border-radius: 10px;
      z-index: 2000;

      &::before,
      &::after {
        position: absolute;
        content: "";
        top: 9px;
        left: 6px;
        width: 22px;
        height: 2px;
        border-radius: 2px;
        background-color: $color-default-white;
        box-shadow: 0 7px 0 0 $color-default-white, 0 14px 0 0 $color-default-white;
        transform: rotate(0);
        will-change: transform;
        transition: transform 0.3s ease-in, box-shadow 0.5s ease-in;
      }
    }
  }

  .user-menu__nav-btn.active {
    @include vp-1023 {
      background: rgba(255, 255, 255, 0.80);

      &::before,
      &::after {
        top: 16px;
        left: 9px;
        width: 16px;
        box-shadow: none;
        background-color: $color-default-black;
        will-change: transform;
        transition: transform 0.3s ease-in;
      }

      &::before {
        transform: rotate(45deg);
      }

      &::after {
        transform: rotate(-45deg);
      }
    }
  }

  .user-menu__btn--login {
    background: transparent;

    &:hover {
      color: $color-default-white;
      border: 1px solid $color-default-white;
    }
  }

  .user-menu__btn--signup {
    background: rgba(62, 64, 70, 0.3);
    border: none;

    &:hover {
      color: $color-default-white;
      background: #3E4046;
    }
  }

  .header-nav {
    display: flex;
    order: -2;
    margin-right: auto;

    @include vp-1023 {
      display: none;
    }

    .adblock {
      display: none;
    }
  }

  .header-nav.active {
    display: block;
    top: -15px;
    padding: 100px 12px 32px;
    min-width: 345px;
    border-radius: 20px 0px 0px 20px;
    background: #0E0C04;
    box-shadow: 0px 0px 50px 0px #000;
  }

  .header-nav__content {
    display: flex;
    padding: 0;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  .header-nav__list {
    display: flex;
    margin: 0;
    padding: 0;

    @include vp-1023 {
      flex-direction: column;
    }
  }

  .header-nav__item {
    position: relative;
    width: fit-content;
    height: fit-content;
    margin: 0;
    margin-right: 15px;
    will-change: background-color;
    transition: background-color 0.3s ease-in;

    &--courses {
      display: none;
    }

    &--forum {
      order: -2;
    }

    &--catalog {

      .header-nav__link h3 {
        display: none;
      }
    }

    &--blog {
      order: -1;
    }

    &::before {
      display: none;
    }

    &:hover {
      background-color: #3E3613;
    }

    @include vp-1279 {
      margin-right: 10px;
    }

    @include vp-1023 {
      min-width: 100%;
      margin: 0;
      margin-bottom: 10px;

      &:hover {
        background-color: transparent;
      }
    }
  }

  .header-nav__item--divider {
    display: flex;
    align-items: center;
    padding: 0 10px;

    div {
      width: 1px;
      height: 28px;
      background: $color-default-white;
      opacity: 0.7;
      margin: 7px 0;
    }

    &:hover {
      background-color: transparent;
    }

    @include vp-1279 {
      display: none;
    }
  }

  .header-nav__item--allstars,
  .header-nav__item--allies {
    position: relative;

    h3 {
      padding-left: 41px !important;
    }

    &::before {
      display: block;
      position: absolute;
      content: "";
      top: 50%;
      transform: translateY(-50%);
      left: 10px;
      width: 20px;
      height: 20px;
      border-radius: none;
      border: none;
    }
  }

  .header-nav__item--allstars {
    &::before {
      background: url("../img/svg/nav-allstars.svg") center no-repeat;
      background-size: contain;
    }

    &:hover {
      &::before {
        background: url("../img/svg/nav-allstars-hover.svg") center no-repeat;
        background-size: contain;
      }
    }
  }

  .header-nav__item--allies {
    &::before {
      background: url("../img/svg/nav-allies.svg") center no-repeat;
      background-size: contain;
    }

    &:hover {
      &::before {
        background: url("../img/svg/nav-allies-hover.svg") center no-repeat;
        background-size: contain;
      }
    }
  }

  .header-nav__link {
    padding: 0;

    h3 {
      @include font-settings ($ff-inter, 500, 14px, 1);
      color: $color-default-white;
      text-transform: none;
      opacity: 0.7;
      border: none;
      border-radius: 10px;
      padding: 14px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;

      @include vp-1023 {
        justify-content: flex-start;
      }
    }

    p {
      display: none;
    }

    .header-nav__heading {
      display: none;
    }

    .header-nav__heading--wc {
      display: block;
    }
  }

  .wc {
    @include vp-767 {
      margin-top: -70px;
    }
  }

  .header-nav__socials {
    @include vp-767 {
      display: block;
      margin-top: 40px;
    }
  }

  .header-nav__socials-list {
    display: flex;
    justify-content: center;
  }

  .header-nav__socials-item {
    width: 44px;
    height: 44px;
    border-radius: 15px;
    background: #1C1C18;

    a {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &:hover {
      background: #262832;
    }

    &:not(:last-child) {
      margin-right: 9px;
    }
  }
}

.wc__nav-btn {
  @include font-settings ($ff-inter, 500, 14px, 1);
  cursor: pointer;
  border: none;
  border-radius: 10px;
  background: transparent url("../img/svg/arrow-down-white.svg") 85% 50% no-repeat;
  color: $color-default-white;
  text-transform: none;
  opacity: 0.7;
  padding: 14px;
  padding-right: 40px;

  @include vp-1023 {
    text-align: start;
    width: 100%;
    background-position: 24% 50%;
  }
}

.wc__nav-btn.active {
  background-color: #3E3613;
}

.wc__nav-item:not(:last-child) {
  margin-right: 22px;
}

.wc__catalog-list {
  display: none;
}

.wc__catalog-list.active {
  position: absolute;
  top: 47px;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  min-width: 304px;
  height: auto;
  padding: 8px 4px 4px 1px;
  gap: 4px;
  border-radius: 12px;
  background: #3E3613;
  backdrop-filter: blur(22px);
  z-index: 100;

  @include vp-1023 {
    position: relative;
    top: 0;
    margin-top: 5px;
  }
}

.wc__catalog-item {
  width: 100%;
  padding: 8px 16px;

  a {
    @include font-settings ($ff-inter, 400, 14px, 1.71);
    color: $color-default-white;
    display: block;
  }

  &:hover {
    border-radius: 8px;
    background: rgba(0, 0, 0, 0.10);
  }
}

.wc {
  .adblock {
    position: relative;
    border-radius: 5px;
    border: 0.5px solid rgba(255, 255, 255, 0.3);

    .ad-marker {
      @include font-settings ($ff-inter, 400, 9px, 1.5);
      position: absolute;
      display: block;
      bottom: 5px;
      right: 8px;
      text-align: center;
      padding: 0 5px;
      text-transform: uppercase;
      border-radius: 5px;
      background: rgba(52, 55, 64, 0.60);

      @include vp-767 {
        display: none;
      }
    }
  }
}

.wc-info {
  .container {
    .adblock:nth-child(2) {
      @include vp-1279 {
        order: 1;
      }
    }
  }
}

.wc-description-secondary {
  .container {
    .adblock {
      order: 1;

      &:nth-child(1) {
        @include vp-1279 {
          order: 0;
        }
      }
    }
  }
}