.static-content {
  margin-top: 20px;
  background: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 36px 45px 30px;

  @include vp-767 {
    padding: 20px 15px;
  }
}

.static-content__heading {
  h2 {
    @include font-settings ($ff-inter, 700, 60px, 1.2);
    letter-spacing: 0.0075em;
    color: #D1D2D3;
    margin-bottom: 20px;

    @include vp-1023 {
      font-size: 42px;
    }

    @include vp-767 {
      font-size: 28px;
    }
  }
}

.static-content__text {
  h3 {
    @include font-settings ($ff-inter, 500, 18px, 1.56);

    @include vp-767 {
      font-size: 14px;
    }
  }

  p {
    @include font-settings ($ff-inter, 500, 18px, 1.56);
    color: #D1D2D3;
    margin-bottom: 27px;

    @include vp-767 {
      font-size: 14px;
    }
  }
}
