.user-menu {
  position: relative;
  display: flex;
  margin-right: -9px;

  @include vp-767 {
    margin-right: -15px;
  }
}

.user-menu__lang-switcher {
  position: relative;
  width: 39px;
  order: -1;
  margin-right: 16px;

  @include vp-767 {
    margin-right: 0;
    margin-left: 10px;
  }
}

.user-menu__lang-switcher-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  padding: 13px 10px 5px;

  .user-menu__lang-link {
    @include font-settings ($ff-inter, 500, 14px, 1);
    color: $color-white-opacity-50;
    display: none;
    margin-bottom: 8px;

    &:hover {
      text-decoration: underline;
    }

    &.active {
      display: block;
      order: -1;
    }
  }

  &:hover {
    background: #1a1d24;
    box-shadow: 0px 0px 1px 1px #2d3341;

    .user-menu__lang-link {
      display: block;
    }
  }
}

.user-menu__logged,
.user-menu__unlogged {
  display: none;

  @include vp-767 {
    opacity: 0;
    height: 0;
  }
}

// MENU LOGGED

.user-menu__logged.active {
  display: flex;
  position: relative;
  align-items: center;
  margin-right: 8px;

  @include vp-767 {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 17px;
    padding-left: 10px;

    &::before {
      position: absolute;
      content: "";
      bottom: 0;
      right: 2px;
      width: 96%;
      height: 0.5px;
      background-color: #E6E6E7;
    }
  }
}

.user-menu__user-block {
  display: flex;
  align-items: center;
  margin-bottom: 10px;

  @include vp-767 {
    margin-bottom: 14px;
  }
}

.user-menu__avatar {
  width: 30px;
  height: 30px;
  background: url("../img/admin-icon.png") center no-repeat;
  background-size: cover;
  border-radius: 10px;
  margin-top: 5px;
  overflow: hidden;

  @include vp-767 {
    margin-right: 19px;
  }

  a {
    display: block;
    width: 30px;
    height: 30px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.user-menu__user-name {
  display: none;

  @include vp-767 {
    @include font-settings ($ff-inter, 600, 14px, 1);
    display: block;
    text-transform: uppercase;
  }
}

.user-menu__logout-btn {
  display: none;

  @include vp-767 {
    display: flex;
    align-items: center;
    max-width: 80px;
    background-color: transparent;
    border: none;
    margin-bottom: 16px;
    padding: 0;
    cursor: pointer;

    span {
      @include font-settings ($ff-inter, 500, 12px, 2);
      color: #D1D2D3;
      margin-left: 10px;
    }
  }

  &:hover {
    opacity: 0.6;
  }
}

// UNLOGGED USERMENU

.user-menu__unlogged.active {
  display: flex;
  align-items: center;

  @include vp-767 {
    flex-direction: column;
    padding: 0px 10px;
    margin-bottom: 10px;
  }
}

.user-menu__btn {
  @include font-settings ($ff-inter, 500, 14px, 1);
  min-height: 42px;
  border-radius: 10px;
  cursor: pointer;

  &--login {
    color: $color-white-opacity-50;
    min-width: 62px;
    background: #262931;
    border: 1px solid rgba(255, 255, 255, 0.5);
    margin-right: 16px;

    &:hover {
      background-color: #1A1D25;
    }
  }

  &--signup {
    color: rgba(255, 255, 255, 0.8);
    min-width: 118px;
    background-color: #3E4046;
    border: 1px solid #3E4046;

    &:hover {
      background-color: #76777C;
      border: 1px solid #76777C;
    }
  }

  @include vp-767 {
    min-width: 100%;
    margin: 0 0 10px;
  }
}

// ALERTS

.user-menu__alerts {
  margin-right: 17px;
  margin-top: 1px;

  @include vp-767 {
    display: none;
  }
}

.user-menu__alerts-btn {
  width: 41px;
  height: 42px;
  position: relative;
  background-color: transparent;
  border: none;
  cursor: pointer;

  &:hover,
  &.active {
    background-color: #3E4046;
    border-radius: 6px;

    span {
      border: 2px solid #3E4046;
    }
  }
}

.user-menu__alerts-btn-icon {
  pointer-events: none;
}

.user-menu__alerts-icon {
  position: absolute;
  top: 4px;
  left: 18px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 2px solid #1E2228;
  background-color: #FF0000;
  pointer-events: none;
}

.user-menu__alerts-list {
  display: none;
}

.user-menu__alerts-list.active {
  display: block;
  position: absolute;
  top: 29px;
  left: -147px;
  background-color: #3E4046;
  border-radius: 6px 0px 6px 6px;
  padding: 10px 20px 12px 3px;

  h3 {
    @include font-settings ($ff-inter, 700, 14px, 1);
  }

  a {
    @include font-settings ($ff-inter, 600, 14px, 1);
    padding: 10px 0;
  }
}

.user-menu__alerts-item {
  padding: 7px 32px 3px 25px;

  &.active {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      top: 13px;
      left: 5px;
      width: 10px;
      height: 10px;
      background-color: #FF0000;
      border-radius: 50%;
    }
  }

  &:not(:first-child):hover {
    background-color: #2F3239;
  }
}

// NAV MENU BUTTON

.user-menu__nav-btn {
  background: transparent url("../img/sprite/apps.svg") center no-repeat;
  width: 41px;
  height: 42px;
  border: none;
  cursor: pointer;
  margin-left: 10px;

  &:hover,
  &.active {
    background-color: #3E4046;
    border-radius: 6px;
  }

  @include vp-767 {
    background: transparent url("../img/sprite/apps-mob.svg") center no-repeat;
    margin-right: 15px;
  }
}

.user-menu__nav-btn.active {
  @include vp-767 {
    background: #1E2228 url("../img/sprite/mob-menu-close-btn.svg") center no-repeat;
  }
}
