.dubai {
  .container {
    padding: 0;
    max-width: 1440px;
  }
}

.dubai__intro {
  .container {
    position: relative;
    display: flex;
    justify-content: center;
    padding-top: 88px;
    padding-bottom: 250px;

    @include vp-767 {
      padding-top: 52px;
      padding-bottom: 133px;
    }
  }
}

.dubai__heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 20;
  pointer-events: none;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.dubai__logo {
  width: 241px;
  height: 37px;
  margin-bottom: 16px;

  @include vp-1023 {
    width: 118px;
    height: 18px;
    margin-bottom: 2px;
  }
}

.dubai__title {
  width: 960px;
  height: 106px;
  margin-bottom: 386px;

  @include vp-1023 {
    width: 296px;
    height: 33px;
    margin-bottom: 143px;
  }
}

.dubai__subtitle {
  width: 453px;
  height: 47px;
  margin-bottom: 7px;

  @include vp-1023 {
    width: 190px;
    height: 20px;
    margin-bottom: 0;
  }
}

.dubai__info {
  @include font-settings ($ff-inter, 600, 14px, 1.1);
  text-align: center;
  text-transform: uppercase;

  @include vp-1023 {
    font-size: 13px;
  }
}

.dubai__intro-image {
  position: absolute;

  &--1 {
    width: 23.2%;
    top: 109px;
    left: 56px;

    @include vp-1023 {
      width: 31.7%;
      top: 63px;
      left: -27px;
    }
  }

  &--2 {
    width: 47.6%;
    top: 242px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 3;

    @include vp-1023 {
      width: 69.6%;
      top: 102px;
    }
  }

  &--3 {
    width: 25.6%;
    top: 122px;
    right: -44px;
    z-index: 2;

    @include vp-1023 {
      width: 30.6%;
      top: 11px;
      right: -30px;
    }
  }

  &--4 {
    width: 32.4%;
    top: 577px;
    left: -23px;

    @include vp-1023 {
      width: 49.6%;
      top: 231px;
      left: -44px;
    }
  }

  &--5 {
    width: 37.6%;
    top: 449px;
    right: 18px;

    @include vp-1023 {
      width: 54.2%;
      top: 236px;
      right: -57px;
    }
  }

  &:hover {
    z-index: 10;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.dubai__text-s {
  @include font-settings ($ff-inter, 400, 22px, 1.6);

  @include vp-1023 {
    max-width: 100%;
  }

  @include vp-767 {
    font-size: 14px;
  }
}

.dubai__text-m {
  @include font-settings ($ff-inter, 400, 30px, 1.6);
  text-align: center;
  max-width: 70%;
  margin: 0 auto 121px;
  padding: 0 20px;
  opacity: 0.8;

  @include vp-1023 {
    max-width: 100%;
  }

  @include vp-767 {
    font-size: 14px;
    padding: 0px 10px;
    margin-bottom: 60px;
  }

  a {
    text-decoration: underline;
    text-underline-offset: 6px;

    @include vp-767 {
      text-underline-offset: 3px;
    }
  }
}

.dubai__text-l {
  @include font-settings ($ff-ttt, 700, 39px, 1.6);
  text-transform: uppercase;
  margin-left: 118px;

  b {
    font-weight: 800;
  }

  @include vp-1023 {
    margin-left: 0;
  }
}


.dubai__description {
  .container {
    padding: 0 20px;
  }
}

.dubai__description--1 {
  .container {
    padding-top: 37px;
    padding-bottom: 62px;

    @include vp-767 {
      padding: 0 0 58px;
    }
  }

  .dubai__text-l {
    max-width: 65%;

    @include vp-1023 {
      max-width: 90%;
    }

    @include vp-767 {
      font-size: 23px;
      margin-left: 20px;
    }
  }
}

.dubai__description--2 {
  .container {
    padding: 55px 0 114px;

    @include vp-767 {
      padding: 38px 10px 77px 20px;
    }
  }

  .dubai__text-s {
    max-width: 67%;
    margin: 0 auto;

    @include vp-767 {
      max-width: 100%;
    }
  }
}

.dubai__description--3 {
  .container {
    padding-top: 109px;
    padding-bottom: 63px;

    @include vp-767 {
      padding-top: 67px;
      padding-bottom: 55px;
    }
  }

  .dubai__text-l {
    font-size: 29px;
    max-width: 75%;
    margin-bottom: 28px;

    @include vp-1023 {
      max-width: 100%;
    }

    @include vp-767 {
      font-size: 23px;
    }
  }

  .dubai__text-s {
    font-size: 23px;
    max-width: 65%;
    margin-left: 315px;

    @include vp-1023 {
      max-width: 100%;
    }

    @include vp-767 {
      margin-left: 34px;
      font-size: 14px;
    }
  }
}

.dubai__partners {
  .container {
    padding: 109px 20px 114px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include vp-767 {
      padding: 59px 20px 40px;
    }
  }

  h2 {
    @include font-settings ($ff-inter, 400, 23px, 1.6);
    text-align: center;
    margin-bottom: 10px;

    @include vp-767 {
      font-size: 20px;
    }
  }
}

.dubai__partners-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1160px;

  &--primary {
    .dubai__partners-item {
      width: 450px;
      height: 200px;

      @include vp-767 {
        width: 329px;
        height: 146px;
      }
    }
  }

  &--secondary {
    .dubai__partners-item {
      width: 280px;
      height: 152px;

      @include vp-767 {
        width: 163px;
        height: 89px;
      }
    }
  }
}

.dubai__partners-item {
  position: relative;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    filter: saturate(0%) brightness(1.4);
    opacity: 0.8;
    transition: all 0.3s ease-in;
  }
}

.dubai__partners-item:hover img {
  filter: none;
  opacity: 1;
  transition: all 0.3s ease-in;
}
