.pagination {
  background-color: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 0 8px;

  @include vp-1023 {
    justify-content: center;
    min-width: 290px;
    margin-bottom: 12px;
  }
}

.pagination__icon {
  width: 26px;
  height: 26px;

  &--last,
  &--next {
    transform: scale(-1, 1);
    fill: $color-accent;
  }
}

.pagination__link {
  @include font-settings ($ff-inter, 500, 14px, 1);
  color: $color-accent;
  padding: 14px 0 17px;

  &:hover {
    background-color: rgb(255, 255, 255, 0.1);
  }

  &:hover,
  &:hover svg,
  &.active {
    color: $color-default-white;
    fill: $color-default-white;
  }

  span {
    padding: 0 10px;
  }
}

.pagination .pagination__link--prev {

  @include vp-1023 {
    margin-right: 45px;
  }
}

.pagination .pagination__link--next {

  @include vp-1023 {
    margin-left: 45px;
  }
}

.pagination__link--prev,
.pagination__link--next,
.pagination__link--first,
.pagination__link--last {
  padding: 8px 0 9px;
}
