.comment {
  width: 100%;
  min-height: 372px;
  display: grid;
  grid-template-columns: 170px auto;
  background-color: #262931;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 19px;

  @include vp-1023 {
    grid-template-columns: 1fr;
    padding: 0;
    min-height: 0;
  }
}

.comment__user-block {
  padding-right: 4px;
  border-right: 1px solid #32343C;

  @include vp-1023 {
    display: grid;
    grid-template-columns: 48px auto;
    column-gap: 7px;
    border-right: none;
    border-bottom: 1px solid #32343c;
    padding: 20px 20px 12px;
  }
}

.comment__user-image {
  position: relative;
  width: 150px;
  height: 150px;
  background-color: #1A1D25;
  border-radius: 20px;
  margin-bottom: 9px;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include vp-1023 {
    width: 48px;
    height: 48px;
    border-radius: 9px;
    margin: 0;
  }
}

.comment__abbr {
  @include font-settings ($ff-inter, 600, 20px, 1.6);
  text-transform: uppercase;
  color: #D1D2D3;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.comment__user-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-right: 15px;

  @include vp-1023 {
    display: grid;
    grid-template-columns: 80px auto;
    column-gap: 10px;
    padding: 0;
  }
}

.comment__user-name {
  @include font-settings ($ff-inter, 700, 16px, 1);
  margin-bottom: 8px;

  @include vp-1023 {
    margin: 0;
  }
}

.comment__user-status {
  @include font-settings ($ff-inter, 400, 16px, 1);
  max-width: 89px;
  color: $color-accent;
  border: 1px solid $color-accent;
  border-radius: 140px;
  margin-bottom: 14px;
  padding: 5px 10px;

  @include vp-1023 {
    max-width: 75px;
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    margin: 0;
    font-size: 12px;
    padding: 3px 10px 6px;
  }
}

.comment__user-stats {
  width: 100%;
  margin-bottom: 7px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  span {
    @include font-settings ($ff-inter, 300, 14px, 1);
    opacity: 0.5;

    @include vp-1023 {
      font-size: 12px;
      padding: 9px 0 0 33px;
    }
  }

  b {
    @include font-settings ($ff-inter, 600, 14px, 1);
    color: $color-accent;
    word-break: break-word;
    margin-left: 5px;

    @include vp-1023 {
      font-size: 12px;
      padding: 5px;
      margin: 6px 0 0 29px;
    }
  }

  &--msg {
    b {
      background-color: #2C2C2D;
      border-radius: 10px;
      min-width: 46px;
      padding: 10px 5px;
      text-align: center;
    }

    @include vp-1023 {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &--city {
    @include vp-1023 {
      display: none;
    }
  }

  @include vp-1023 {
    grid-column: 2 / 3;
    grid-row: 1 / 3;
  }
}

.comment__user-contacts {
  @include font-settings ($ff-inter, 500, 14px, 1);
  width: 100%;
  background: #1E2228;
  border-radius: 10px;
  color: $color-accent;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 7px;
  padding: 10px;
  word-break: break-word;

  &:hover {
    color: $color-default-white;
  }

  span {
    padding-right: 11px;
  }

  @include vp-1023 {
    display: none;
  }
}

.comment__user-contacts-icon {
  min-width: 20px;
  min-height: 18px;
}

.comment__text-block {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-left: 20px;

  @include vp-1023 {
    padding: 14px 19px 20px;
  }
}

.comment__post-time {
  flex-shrink: 0;
  margin-bottom: 36px;

  p {
    @include font-settings ($ff-inter, 500, 14px, 1.5);
    opacity: 0.5;
  }

  @include vp-1023 {
    margin-bottom: 9px;
  }
}

.comment__text {
  flex-grow: 1;
  padding-right: 85px;
  padding-bottom: 15px;

  @include vp-1023 {
    padding: 0;
    margin-bottom: 45px;
  }

  p {
    @include font-settings ($ff-inter, 500, 14px, 1.5);
    margin-bottom: 10px;

    @include vp-1023 {
      font-weight: 400;
    }
  }

  h2 {
    @include font-settings ($ff-inter, 400, 20px, 1.5);
    margin: 0 0 6px;
    color: $color-accent;
  }

  h3 {
    @include font-settings ($ff-inter, 400, 18px, 1.5);
    margin: 0 0 6px;
    color: $color-accent;
  }

  h4 {
    @include font-settings ($ff-inter, 400, 16px, 1.5);
    margin: 0 0 6px;
    color: $color-accent;
  }

  ul,
  ol {
    margin: 0 0 10px;
    padding: 0 0 0 23px;
  }

  li {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
  }

  img {
    width: 100%;
    height: auto;
    object-fit: cover;
    margin-bottom: 10px;
  }

  a {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    font-style: normal;
    color: $color-accent;
    text-decoration: underline;
    margin-bottom: 12px;
  }

  code {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  pre {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    white-space: pre-wrap;
    word-wrap: break-word;
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  blockquote {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    padding-left: 16px;
    border-left: 2px solid #DEE2E6;
    margin: 10px 0;
  }

  hr {
    height: 1px;
    margin: 12px 0;
    background-color: #DEE2E6;
    border: none;
  }
}

.comment__btns-block {
  min-height: 37px;
  display: flex;
  flex-shrink: 0;
  background-color: #1E2228;
  border-radius: 10px;
  justify-self: flex-end;
  justify-content: space-between;

  @include vp-767 {
    flex-direction: column-reverse;
    padding: 0 10px;
  }
}

.comment__btns-primary {
  display: flex;

  @include vp-767 {
    justify-content: space-between;
  }
}

.comment__btns-secondary {
  display: flex;

  @include vp-767 {
    justify-content: space-evenly;
    padding: 0 34px;
  }
}

.comment__btn {
  color: $color-accent;
  border: none;
  background-color: transparent;
  opacity: 0.8;
  padding: 5px 17px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  &:not(:last-child) {
    border-right: 1px solid #2E3136;

    @include vp-1023 {
      border: none;
    }
  }

  &:hover {
    background-color: #262832;
  }

  @include vp-1023 {
    width: 37px;
    height: 37px;
    padding: 0;

    svg {
      width: 24px;
      height: 24px;
      margin: 0;
    }
  }
}

.comment__btn svg {
  margin-right: 7px;
}

.comment__btn--edit {
  display: none;

  @include vp-767 {
    display: block;
  }
}

.comment__btn--expand {
  display: none;

  @include vp-767 {
    display: block;

    b {
      @include font-settings ($ff-inter, 500, 18px, 1);
      color: $color-accent;
      display: block;
      padding-bottom: 7px;
    }

    &.active b {
      color: $color-default-white
    }
  }
}

.comment__btns-secondary {
  @include vp-767 {
    display: none;
  }

  .comment__btn {
    padding: 5px 12px;

    @include vp-1023 {
      padding: 0;
    }
  }

  .comment__btn--edit {
    display: block;

    @include vp-767 {
      display: none;
    }
  }
}

.comment__btns-block.active .comment__btns-secondary {
  display: flex;
}
