.advert-popup {
  display: none;
}

.advert-popup.active {
  display: flex;
  flex-direction: column;
  position: fixed;
  bottom: 20px;
  left: 20px;
  transition: 0.5s all;
  width: 360px;
  height: auto;
  background-color: #101116;
  padding: 4px 7px;
  box-shadow: 0px 12px 24px -6px rgba(96, 96, 96, 0.15), 0px 0px 1px rgba(96, 96, 96, 0.1);
  border-radius: 6px;
  z-index: 1000;

  @include vp-767 {
    right: 0;
  }
}

.advert-popup__close-btn {
  width: 17px;
  height: 17px;
  padding: 10px;
  background: transparent url("../img/sprite/mob-menu-close-btn.svg") center no-repeat;
  border: none;
  margin-left: auto;
  cursor: pointer;
}

.advert-popup__content {
  padding: 8px 18px 4px 14px;
}

.advert-popup__image {
  width: 100%;
  height: 145px;
  border-radius: 8px;
  overflow: hidden;
  margin-bottom: 12px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.advert-popup__info {
  display: flex;

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.1);
    margin-right: 5px;
  }
}

.advert-popup__link {
  @include font-settings ($ff-inter, 500, 14px, 1.2);
  display: flex;
  justify-content: center;
  align-items: center;
  color: $color-default-black;
  min-width: 100px;
  height: 33px;
  background: #FCCD03;
  box-shadow: 0px 2px 2px -2px rgba(34, 38, 50, 0.3);
  border: none;
  border-radius: 6px;
  margin-left: auto;
  cursor: pointer;

  &:hover {
    background-color: $color-accent-hover;
  }
}
