.updates {
  background-color: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 37px 38px 30px;

  @include vp-767 {
    padding: 0;
    border: none;
  }

  h2 {
    @include font-settings ($ff-inter, 500, 30px, 1.16);
    letter-spacing: 0.96px;
    margin-bottom: 25px;

    @include vp-767 {
      display: none;
    }
  }
}

.updates__content {
  position: relative;
  background-color: #12151B;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 56px 38px 20px 32px;

  @include vp-767 {
    background-color: #1A1D24;
    padding: 20px 20px 0px;
  }
}

.updates__cover {
  position: relative;
  width: 100%;
  height: 210px;
  border-radius: 20px;
  margin-bottom: 16px;
  overflow: hidden;

  @include vp-1023 {
    margin-bottom: 19px;
  }

  &::before {
    position: absolute;
    content: "";
    width: calc(100% + 2px);
    height: calc(100% + 2px);
    background: linear-gradient(180deg, rgba(38, 41, 49, 0) 15%, #262931 100%);
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.updates__author {
  display: flex;
  align-items: center;
  margin-bottom: 24px;

  @include vp-1023 {
    margin-bottom: 13px;

  }

  p,
  a {
    @include font-settings ($ff-inter, 400, 14px, 1.1);
    color: #aaaaaa;
    letter-spacing: 0.96px;
    text-transform: uppercase;

    @include vp-1023 {
      color: #ffffff;
      font-size: 13px;
    }
  }

  .updates__datetime {
    color: #ffffff;
    position: absolute;
    top: 72px;
    right: 77px;
    letter-spacing: 0.46px;
    background: rgba(0, 0, 0, 0.35);
    backdrop-filter: blur(5px);
    border-radius: 5px;
    padding: 5px;
    max-height: fit-content;

    @include vp-767 {
      position: relative;
      top: 0;
      right: 0;
      background: transparent;
      backdrop-filter: none;
      padding: 0;
      margin-left: auto;
    }
  }
}

.updates__author-img {
  position: relative;
  width: 36px;
  height: 36px;
  background: url("../img/admin-icon.png") center no-repeat;
  background-size: cover;
  border-radius: 50%;
  margin-right: 10px;
  overflow: hidden;

  @include vp-1023 {
    width: 25px;
    height: 25px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.updates__abbr {
  @include font-settings ($ff-inter, 600, 13px, 1.6);
  text-transform: uppercase;
  color: #D1D2D3;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.updates__text-heading {
  position: absolute;
  display: flex;
  align-items: flex-end;
  top: 56px;
  left: 34px;
  width: 90%;
  height: 210px;
  padding: 20px;

  @include vp-767 {
    position: relative;
    display: block;
    width: fit-content;
    height: fit-content;
    padding: 0;
    top: 0;
    left: 0;
    margin-bottom: 13px;
  }

  h3 {
    @include font-settings ($ff-inter, 700, 35px, 0.99);
    letter-spacing: 0.97px;
    text-transform: uppercase;

    @include vp-767 {
      font-size: 24px;
      line-height: 1.45;
    }
  }
}

.updates__text {
  margin-bottom: 48px;
  padding-left: 6px;

  @include vp-1023 {
    padding: 0;
    margin-bottom: 44px;
  }

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin-bottom: 25px;
  }

  h2 {
    @include font-settings ($ff-inter, 400, 26px, 1.5);
    margin: 0 0 6px;
    color: $color-accent;
  }

  h3 {
    @include font-settings ($ff-inter, 400, 24px, 1.5);
    margin: 0 0 6px;
    color: $color-accent;
  }

  h4 {
    @include font-settings ($ff-inter, 400, 22px, 1.5);
    margin: 0 0 6px;
    color: $color-accent;
  }

  ul,
  ol {
    margin: 0 0 25px;
    padding: 0 0 0 23px;
  }

  li {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
  }

  img {
    width: 100%;
    height: auto;
    object-fit: cover;
    margin-bottom: 10px;
  }

  a {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    font-style: normal;
    color: $color-accent;
    text-decoration: underline;
    margin-bottom: 12px;
  }

  code {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  pre {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    white-space: pre-wrap;
    word-wrap: break-word;
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  blockquote {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    padding-left: 16px;
    border-left: 2px solid #DEE2E6;
    margin: 10px 0;
  }

  hr {
    height: 1px;
    margin: 12px 0;
    background-color: #DEE2E6;
    border: none;
  }
}

.updates__comments {
  padding-left: 6px;

  @include vp-1023 {
    padding: 0;
  }
}
