.rally {
  .container {
    padding: 0;
    max-width: 1440px;
  }
}

.rally__intro {
  .container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../img/events/intro-bg.png") center no-repeat;
    background-position-y: -73px;
    padding-top: 207px;
    padding-bottom: 176px;

    @include vp-767 {
      background-position: 30% -131px;
      padding-top: 160px;
      padding-bottom: 156px;
    }

    &::after {
      position: absolute;
      content: "";
      top: -22px;
      left: -44px;
      width: 100%;
      height: 100%;
      background: url("../img/events/intro-line.svg") center no-repeat;
      z-index: 100;

      @include vp-1023 {
        display: none;
      }
    }
  }
}

.rally__heading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 20px;

  h1 {
    @include font-settings ($ff-ttt, 700, 57px, 1);
    text-transform: uppercase;
    text-align: center;
    max-width: 70%;
    margin: 0;
    margin-bottom: 20px;

    @include vp-1279 {
      max-width: 90%;
    }

    @include vp-1023 {
      max-width: 100%;
      font-size: 40px;
    }

    @include vp-767 {
      font-size: 22px;
    }
  }

  .rally__intro-image {
    display: flex;
    margin-bottom: 20px;

    @include vp-767 {
      flex-direction: column
    }

    img {
      margin-right: 30px;

      @include vp-767 {
        margin-right: 0;
        transform: scale(0.91)
      }
    }
  }

  .rally__sub {
    @include font-settings ($ff-inter, 600, 14px, 1.1);
    text-align: center;
    text-transform: uppercase;
  }
}

.rally__description {
  .container {
    padding-top: 50px;

    @include vp-767 {
      margin-top: -100px;
      padding-top: 0;
    }
  }

  p {
    @include font-settings ($ff-inter, 500, 30px, 1.6);
    text-align: center;
    max-width: 70%;
    margin: 0 auto;
    margin-bottom: 40px;
    padding: 0 20px;
    position: relative;
    z-index: 100;

    @include vp-1023 {
      max-width: 100%;
    }

    @include vp-767 {
      font-size: 14px;
    }
  }

  a {
    text-decoration: underline;
  }
}

.rally__text {
  .container {
    padding: 74px 0;

    @include vp-767 {
      padding: 30px 20px 57px;
    }
  }

  p {
    @include font-settings ($ff-inter, 400, 22px, 1.6);
    max-width: 67%;
    margin: 0 auto;

    @include vp-1023 {
      max-width: 100%;
      margin-left: 18px;
    }

    @include vp-767 {
      font-size: 14px;
    }
  }
}

.rally__partners {
  .container {
    padding: 95px 0 124px;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include vp-767 {
      padding: 42px 20px 53px;
    }
  }

  h2 {
    @include font-settings ($ff-inter, 400, 23px, 1.6);
    text-align: center;
    margin-bottom: 59px;

    @include vp-767 {
      font-size: 15px;
    }
  }
}

.rally__partners-list {
  display: grid;
  grid-template-columns: repeat(4, auto);
  gap: 46px 40px;

  @include vp-1279 {
    grid-template-columns: repeat(2, auto);
    gap: 46px 24px;
  }
}

.rally__partners-item {
  position: relative;
  width: 260px;
  height: 99px;

  @include vp-767 {
    width: 156px;
    height: 59px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    filter: saturate(0%) brightness(1.4);
    opacity: 0.8;
    transition: all 0.3s ease-in;
  }
}

.rally__partners-item:hover img {
  filter: none;
  opacity: 1;
  transition: all 0.3s ease-in;
}
