.courses-cards {
  background: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 50px 40px 38px;

  @include vp-767 {
    padding: 20px 14px;
  }
}

.courses-cards__heading {
  h2 {
    @include font-settings ($ff-inter, 700, 35px, 0.99);
    letter-spacing: 0.96px;
    text-transform: uppercase;
    margin-bottom: 44px;

    @include vp-767 {
      font-size: 20px;
      line-height: 1.2;
    }
  }
}

.courses-cards__content {
  display: grid;
  grid-template-columns: auto 360px;
  column-gap: 40px;
  row-gap: 15px;

  @include vp-1279 {
    grid-template-columns: auto 295px;
  }

  @include vp-1023 {
    grid-template-columns: auto;
  }
}

.courses-cards__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 19px 40px;

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.courses-cards__item {
  background: #262931;
  border-radius: 20px;
  max-width: 560px;
  padding: 28px 30px 30px 24px;

  &:hover {
    box-shadow: 0px 0px 20px rgba(10, 57, 184, 0.25);
  }

  &:active {
    opacity: 0.6;
  }

  @include vp-767 {
    padding: 20px;
  }
}

.courses-cards__card {
  h4 {
    @include font-settings ($ff-inter, 400, 13.5px, 1);
    display: inline-block;
    letter-spacing: 0.96px;
    color: #14161B;
    background-color: $color-accent;
    border-radius: 29px;
    padding: 5px 16px;
    margin-bottom: 14px;
  }

  span {
    @include font-settings ($ff-inter, 600, 13.5px, 1);
    letter-spacing: 0.96px;
    color: #14161B;
  }

  h3 {
    @include font-settings ($ff-inter, 700, 20px, 1.74);
    letter-spacing: 0.96px;
    text-transform: uppercase;
    margin-bottom: 9px;
  }

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
    margin-bottom: 18px;

    @include vp-767 {
      font-size: 12px;
    }
  }
}

.courses-cards__tags-list {
  display: flex;
  border-bottom: 1px solid rgba(194, 194, 194, 0.2);
  margin-bottom: 21px;
}

.courses-cards__tags-item {
  letter-spacing: 0.96px;
  padding: 0 9px;
  border: 1px solid #3B3F4A;
  border-radius: 29px;
  margin-right: 12px;
  margin-bottom: 19px;

  &:hover {
    background-color: #1A1D25;
  }

  span {
    @include font-settings ($ff-inter, 400, 13px, 1);
    display: block;
    padding: 5px 0;
  }
}

.courses-cards__duration {
  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
    display: block;
    max-width: 100px;
    background: #3B3F4A;
    border-radius: 10px;
    padding: 12px 11px;
    text-align: center;
    opacity: 0.5;
  }
}
