.user-card {
  display: grid;
  grid-template-columns: 275px auto;
  margin-top: 26px;
  background-color: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;

  @include vp-1023 {
    display: flex;
    flex-direction: column;
    background-color: transparent;
    border: none;
    margin-top: 17px;
  }
}

.user-card__tabs {
  padding: 41px 20px 32px 40px;

  @include vp-767 {
    padding: 17px 0 0;
  }
}

.user-card__tabs-buttons {
  margin-bottom: 32px;

  @include vp-767 {
    margin-bottom: 15px;
    display: flex;
    justify-content: space-between;
  }
}

.user-card__tabs-btn {
  background-color: #262931;
  border-radius: 10px;
  border: none;
  padding: 9px 14px 11px;
  cursor: pointer;

  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
    opacity: 0.5;

    @include vp-767 {
      font-size: 12px;
    }
  }

  &:hover {
    background-color: #76777C;
    color: $color-default-white;
  }

  &:active {
    opacity: 0.5;
  }

  &.active {
    background-color: #FFC600;

    span {
      color: #14161B;
      opacity: 1;
    }
  }

  &:not(:last-child) {
    margin-right: 6px;
  }
}
