.friends__inner-block {
  padding: 13px 17px 40px;
  background: #262931;
  border-radius: 10px;

  @include vp-767 {
    padding: 13px 17px 11px;
  }
}

.friends__heading {
  padding: 2px 0 0 8px;
  margin-bottom: 21px;

  @include vp-767 {
    padding: 6px 0 0 3px;
    margin-bottom: 12px;
  }

  h3 {
    @include font-settings ($ff-inter, 500, 18px, 2.22);
    color: #D1D2D3;
  }
}

.friends__item {
  display: flex;
  align-items: center;
  border-radius: 12px;
  margin-bottom: 18px;

  &--requests {
    padding: 16px 18px 25px 27px;
    background-color: #14161B;

    @include vp-767 {
      flex-direction: column;
      align-items: flex-start;
      margin: 0 3px 12px;
      padding: 16px 27px 21px;
    }

    h4 {
      @include font-settings ($ff-inter, 500, 16px, 1.5);
      color: #D1D2D3;
      margin-bottom: 16px;

      @include vp-767 {
        text-align: center;
      }
    }

    .friends__inner-content {
      display: flex;
      align-items: center;
    }

    .friends__image {
      margin-right: 18px;
      min-width: 56px;
      height: 56px;
      background-color: #1A1D25;

      @include vp-767 {
        margin-right: 18px;
      }
    }

    .friends__user-name {
      padding-bottom: 5px;
    }
  }

  &--accepted {
    padding: 19px 18px 18px 27px;
    background-color: #1A1D25;

    @include vp-767 {
      padding: 11px;
      margin-bottom: 13px;
    }

    .friends__inner-content {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(3, auto) 1fr;
      column-gap: 30px;
      align-items: center;

      @include vp-1023 {
        grid-template-columns: 100px 119px auto;
        column-gap: 0;
      }

      b {
        @include font-settings ($ff-inter, 400, 14px, 1);
        color: $color-accent;
        border: 1px solid $color-accent-opacity-50;
        border-radius: 140px;
        padding: 5px 15px;
        margin-right: auto;

        @include vp-1023 {
          grid-row: 2 / 3;
          grid-column: 2 / 3;
          align-self: start;
          margin-left: 9px;
        }
      }
    }

    .friends__image {
      min-width: 100px;
      height: 100px;
      background-color: #14161B;

      @include vp-1023 {
        grid-row: 1 / 3;
      }
    }

    .friends__user-name {
      @include vp-1023 {
        grid-row: 1 / 2;
        grid-column: 2 / 4;
        padding: 10px 0 0 10px;

        p {
          font-size: 14px;
          line-height: 2;
        }
      }
    }

    .friends__btn--delete {
      @include vp-1023 {
        width: 30px;
        height: 30px;
        grid-row: 2 / 3;
        grid-column: 3 / 4;
        align-self: start;
        margin: 0;

        svg {
          width: 15px;
          height: 15px;
        }
      }
    }
  }
}

.friends__content {
  display: flex;
  flex-direction: column;

  @include vp-767 {
    width: 100%;
  }
}

.friends__btns {
  width: 104px;
  height: 56px;
  background-color: #101116;
  box-shadow: 0px 8px 24px -6px rgba(0, 0, 0, 0.16), 0px 0px 1px rgba(0, 0, 0, 0.4);
  border-radius: 12px;
  margin-left: auto;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  @include vp-767 {
    margin: 19px 0 0 66px;
  }
}

.friends__btn {
  width: 40px;
  height: 40px;
  background-color: #101116;
  border-radius: 8px;
  border: none;
  cursor: pointer;

  &--accept {
    margin-right: 8px;
  }

  &--delete {
    width: 48px;
    height: 48px;
    margin-left: auto;
  }

  &:hover {
    background-color: #262832;
  }
}

.friends__image {
  position: relative;
  border-radius: 8px;
  overflow: hidden;

  img {
    position: inherit;
    z-index: 10;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.friends__abbr {
  @include font-settings ($ff-inter, 600, 13px, 1.6);
  text-transform: uppercase;
  color: #D1D2D3;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.friends__user-name {
  p {
    @include font-settings ($ff-inter, 700, 18px, 1.56);
    letter-spacing: 0.4px;
    text-transform: uppercase;
    color: #D1D2D3;
  }
}
