.blog {
  background-color: #1A1D24;
  border: 1px solid #2D3341;
  border-radius: 10px;
  padding: 31px 20px 44px;

  @include vp-1279 {
    background-color: transparent;
    border: none;
    padding: 0;
    margin-top: 17px;
  }
}

.blog__content {
  position: relative;
  display: grid;
  grid-template-columns: auto 360px;
  grid-template-rows: auto auto;
  column-gap: 40px;
  margin-bottom: 27px;
  padding: 0 20px;

  @include vp-1279 {
    display: flex;
    flex-direction: column;
    padding: 24px 14px 20px;
    background-color: #1A1D24;
    border: 1px solid #2D3341;
    border-radius: 10px;
    margin-bottom: 20px;
  }
}

.blog__heading {
  grid-row: 1 / 2;
  grid-column: 1 / 3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 25px;

  @include vp-1279 {
    padding: 0;
    margin-bottom: 15px;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 35px, 1);
    letter-spacing: 0.9px;
    text-transform: uppercase;

    @include vp-1279 {
      font-size: 25px;
      line-height: 1.2;
    }
  }
}

.blog__btns-wrapper {
  display: flex;
}

.blog__write-btn {
  min-width: 183px;
  background-color: $color-accent;
  color: $color-default-black;
  border: none;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 17px;
  cursor: pointer;

  @include vp-1023 {
    min-width: 47px;
    height: 47px;
    padding: 0;
    margin-left: 12px;
  }

  svg {
    fill: $color-default-black;
    margin-right: 5px;

    @include vp-1023 {
      margin: auto;
    }
  }

  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
    color: $color-default-black;

    @include vp-1023 {
      display: none;
    }
  }

  &:hover {
    background-color: $color-accent-hover;
  }

  &:active {
    opacity: 0.6;
  }
}

.blog__filter-show-btn {
  display: none;

  @include vp-1279 {
    display: block;
    width: 47px;
    height: 47px;
    background-color: #262931;
    border: none;
    border-radius: 10px;
    margin-left: 12px;
    cursor: pointer;

    &:hover {
      background-color: #535761;
    }
  }
}

.blog__articles {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.blog__list {
  display: grid;
  grid-template-columns: repeat(2, auto);
  gap: 31px 26px;
  margin-bottom: 23px;

  @include vp-1279 {
    display: flex;
    flex-wrap: wrap;
  }

  @include vp-767 {
    justify-content: center;
    gap: 20px;
  }
}

.blog__item {
  background-color: #262931;
  border-radius: 20px;
  width: 366px;

  @include vp-1279 {
    width: 290px;
  }

  &.pinned {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      width: 50px;
      height: 50px;
      top: 10px;
      right: 10px;
      background: rgb(38, 41, 49, 0.6) url("../img/svg/pin.svg") center no-repeat;
      border-radius: 10px;
      z-index: 200;

      @include vp-767 {
        width: 40px;
        height: 40px;
        top: 7px;
        right: 7px;
      }
    }
  }

  &:hover {
    box-shadow: 0px 0px 20px rgba(10, 57, 184, 0.25);
  }

  &:active {
    opacity: 0.6;
  }
}

.blog__article-image {
  position: relative;
  display: flex;
  width: 100%;
  height: 205px;
  border-radius: 20px 20px 0px 0px;
  padding: 10px;
  overflow: hidden;

  @include vp-767 {
    height: 163px;
  }

  img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 50;
  }
}

.blog__tags-list {
  display: flex;
  align-self: flex-end;
  flex-wrap: wrap;
  margin-left: 7px;

  @include vp-767 {
    margin: 0;
  }
}

.blog__tags-item {
  background-color: $color-default-white;
  margin-right: 7px;
  margin-bottom: 5px;
  padding: 0px 5px 4px;
  border-radius: 5px;
  z-index: 100;

  @include vp-767 {
    margin-bottom: 0;
    margin-top: 5px;
  }
}

.blog__article-tag {
  @include font-settings ($ff-inter, 400, 12px, 1);
  color: #14161B;
  text-align: center;
  letter-spacing: 0.3px;

  @include vp-767 {
    font-size: 11px;
  }
}

.blog__article-text {
  padding: 32px 34px 34px 20px;

  @include vp-767 {
    padding: 25px 11px 26px 15px;
  }

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.1);
    opacity: 0.5;
    letter-spacing: 0.96px;
    text-transform: uppercase;
    margin-bottom: 15px;

    @include vp-767 {
      font-size: 11px;
      margin-bottom: 13px;
    }
  }
}

.blog__article-link {
  @include font-settings ($ff-inter, 600, 18px, 1.4);
  letter-spacing: 0.3px;

  @include vp-767 {
    font-size: 14px;
    line-height: 1.42;
  }

  &:hover {
    color: $color-accent;
  }
}

.blog__list--second {
  gap: 60px 40px;

  @include vp-1279 {
    margin-top: 25px;
  }

  .blog__item {
    width: 360px;
    display: flex;
    flex-direction: column;

    @include vp-1279 {
      width: 290px;
    }
  }

  .blog__article-image {
    width: 100%;
    height: 214px;

    @include vp-767 {
      height: 169px;
    }
  }

  .blog__article-text {
    padding: 33px 49px 47px 48px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    @include vp-767 {
      padding: 21px 25px 21px 34px;
    }

    p {
      flex-grow: 1;

      @include vp-767 {
        margin-bottom: 20px;
      }
    }
  }

  .blog__article-link {
    @include font-settings ($ff-inter, 600, 24px, 1.42);
    letter-spacing: 0.3px;
    display: block;
    margin-bottom: 18px;

    @include vp-767 {
      font-size: 16px;
      line-height: 1.71;
    }
  }

  p {
    @include font-settings ($ff-inter, 500, 14px, 1);
    color: $color-accent;
    letter-spacing: 0.3px;
    text-transform: none;
    opacity: 1;
    margin-bottom: 24px;
  }
}

.blog__more-btn {
  @include font-settings ($ff-inter, 600, 14px, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 151px;
  background-color: $color-accent;
  color: #14161B;
  border-radius: 5px;
  padding: 16px 27px;
  text-transform: uppercase;
  letter-spacing: 1px;

  @include vp-767 {
    width: 122px;
    font-size: 12px;
    line-height: 0.94;
    padding: 13px 19px;
  }

  &:hover {
    background-color: $color-accent-hover;
  }
}

.blog__cases {

  @include vp-1279 {
    background-color: #1A1D24;
    border: 1px solid #2D3341;
    border-radius: 10px;
    padding: 29px 15px;
  }

  h2 {
    @include font-settings ($ff-inter, 700, 35px, 1);
    letter-spacing: 0.96px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 25px;

    @include vp-1279 {
      font-size: 25px;
      line-height: 1.39;
      margin-bottom: 6px;
    }
  }
}

.blog__cases-list {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 8px;

  @include vp-767 {
    justify-content: center;
    margin-bottom: 5px;
  }
}

.blog__cases-item {
  max-width: 299px;
  min-height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-radius: 20px;
  margin-bottom: 20px;
  padding: 15px 16px 12px;

  @include vp-1279 {
    max-width: 290px;
    min-height: 0;
    padding: 15px 11px 10px;
    margin-bottom: 13px;

    &:last-child {
      margin-bottom: 0;
    }
  }


  &:hover,
  &:active {
    background-color: #262832;
    border-radius: 20px;
  }
}

.blog__cases-image {
  width: 100%;
  height: 207px;
  border-radius: 20px 20px 0px 0px;
  overflow: hidden;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include vp-1279 {
    border-radius: 20px;
  }
}

.blog__cases-link {
  flex-grow: 1;
  padding: 34px 6px 8px 0px;

  @include vp-767 {
    padding: 13px 6px 17px 0;
  }

  a {
    @include font-settings ($ff-inter, 600, 17px, 1.56);
    display: block;
    letter-spacing: 0.29px;
  }
}

.blog__cases-log {
  display: flex;
  align-items: center;
  flex-shrink: 0;

  p,
  a,
  time {
    @include font-settings ($ff-inter, 400, 13px, 1.14);
    color: #AAAAAA;
    letter-spacing: 0.96px;
    text-transform: uppercase;
  }
}

.blog__cases-log-img {
  position: relative;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 13px;
  background-color: #14161B;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.blog__abbr {
  @include font-settings ($ff-inter, 600, 13px, 1.6);
  text-transform: uppercase;
  color: #D1D2D3;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.blog__cases-show-more,
.blog__articles-show-more {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 97%;
  min-height: 47px;
  margin-right: 19px;
  margin-left: 19px;
  background-color: #262931;
  border: none;
  border-radius: 10px;
  color: $color-default-white;
  cursor: pointer;

  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
  }

  svg {
    margin-right: 17px;
  }

  &:hover {
    background-color: #76777C;
    color: $color-default-white;
  }

  &:active {
    opacity: 0.5;
  }
}

.blog__articles-show-more {
  width: 100%;
  margin: 0;
}

.blog__cases-show-more {
  @include vp-1279 {
    width: 100%;
    margin: 0;
  }
}

.blog__filters {
  @include vp-1279 {
    display: none;
  }
}

.blog__filters.active {
  @include vp-1279 {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    border-radius: 20px;
    z-index: 10000;
  }
}

.blog__filter-hide-btn {
  display: none;

  @include vp-1279 {
    @include font-settings ($ff-inter, 500, 14px, 1.43);
    display: block;
    color: #000000;
    position: fixed;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    min-width: 109px;
    height: 40px;
    background-color: $color-accent;
    border: none;
    border-radius: 10px;

    &:hover {
      background-color: $color-accent-hover;
    }
  }
}

.blog__news {
  margin-bottom: 28px;

  @include vp-1279 {
    background-color: #1A1D24;
    border: 1px solid #2D3341;
    border-radius: 10px;
    margin-bottom: 18px;
    padding: 27px 21px 18px;
  }
}
