.block {
  background-color: #262931;
  border-radius: 20px;
}

.block__heading {
  background-color: #1A1D24;
  padding: 13px 11px 8px 20px;
  border-radius: 20px 20px 0 0;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &:not(.block__heading--index) {

    @include vp-1023 {
      background: linear-gradient(0deg, rgba(38, 41, 49, 1) 0%, rgba(38, 41, 49, 1) 50%, rgba(26, 29, 36, 1) 50%, rgba(26, 29, 36, 1) 100%);
      padding-bottom: 0;
      flex-direction: column;
      padding-top: 17px;

      h2 {
        margin-bottom: 25px;
        align-self: flex-start;
      }
    }
  }
}

.block__heading--vip {
  background-color: $color-accent;

  &:not(.block__heading--index) {
    @include vp-1023 {
      background: linear-gradient(0deg, rgba(38, 41, 49, 1) 0%, rgba(38, 41, 49, 1) 50%, $color-accent 50%, $color-accent 100%);
      flex-direction: column;
    }
  }
}

.forum-section--index .block__heading {
  padding: 17px 20px;
}

.block__heading h2 {
  @include font-settings ($ff-inter, 700, 16px, 1);
}

.block__heading--vip h2 {
  color: $color-default-black;
}

.block__list {
  padding: 0 20px;
}

.block__item {
  display: grid;
  grid-template-columns: 403px 31px 162px auto;
  column-gap: 20px;
  padding: 10px 0 8px;

  &:not(:last-child) {
    border-bottom: 1px solid #606368;
  }

  &:last-child {
    padding-bottom: 17px;
  }

  @include vp-1023 {
    grid-template-columns: 1fr;
    padding: 8px 0 16px;

  }
}

.block__col--1 {
  padding-top: 5px;

  @include vp-1023 {
    margin-bottom: 15px;
    padding-top: 10px;
  }

  h3 {
    @include font-settings ($ff-inter, 600, 14px, 1);
    margin-bottom: 3px;

    @include vp-1023 {
      margin-bottom: 5px;
    }
  }

  p {
    @include font-settings ($ff-inter, 300, 14px, 1);

    @include vp-1023 {
      font-size: 12px;
    }
  }
}

.block__col--2 {
  padding-top: 9px;

  .block__add-link {
    &:hover {
      opacity: 0.6;
    }
  }

  .block__add-icon {
    fill: $color-default-white;
    display: block;
    width: 24px;
    height: 24px;
  }

  @include vp-1023 {
    display: none;
  }
}

.block__col--3 {
  padding-top: 5px;

  p {
    @include font-settings ($ff-inter, 400, 14px, 1);
  }

  span {
    @include font-settings ($ff-inter, 600, 14px, 1);
  }

  @include vp-1023 {
    display: none;
  }
}

.block__col--4 {
  a {
    @include font-settings ($ff-inter, 400, 12px, 1.2);
    display: block;
    text-decoration: underline;
    max-width: 208px;
    margin-bottom: 3px;

    &:hover {
      opacity: 0.6;
    }
  }

  p {
    @include font-settings ($ff-inter, 400, 12px, 1);
    display: inline;
  }

  p.block__author {
    a {
      display: inline;
    }
  }
}

.block__logs {
  display: flex;
  justify-content: space-between;

  @include vp-1023 {
    margin-top: 19px;
  }
}

.block__time {
  position: relative;
  padding-left: 25px;

  &::before {
    position: absolute;
    content: url("../img/svg/clock-icon.svg");
    top: -2px;
    left: 0;
  }
}

.block__author {
  position: relative;
  padding-left: 40px;

  &::before {
    position: absolute;
    content: url("../img/svg/user-icon.svg");
    top: 0;
    left: 20px;
  }
}
