.footer {
  .container {
    position: relative;
    background-color: #262931;
    z-index: 10;

    @include vp-767 {
      padding: 0;
    }
  }
}

.footer__content {
  background: #262931;
  border-radius: 10px 10px 0px 0px;
  padding: 39px 9px 27px 40px;

  @include vp-767 {
    padding: 15px;
    border-radius: 0;
  }
}

.footer__nav {
  display: flex;
  justify-content: space-between;
  margin-bottom: 87px;
  margin-right: 70px;

  @include vp-767 {
    flex-direction: column;
    margin: 0;
  }
}

.footer__nav {

  h3,
  h3 a {
    @include font-settings ($ff-inter, 600, 14px, 1);
    text-transform: uppercase;
    margin-bottom: 12px;

    @include vp-767 {
      font-weight: 400;
      margin-bottom: 0;
      text-transform: none;
      color: #B8B8B8;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  h3 a {
    &:hover {
      opacity: 0.6;
    }
  }
}

.footer__item {
  margin-bottom: 4px;

  a {
    @include font-settings ($ff-inter, 400, 14px, 1);
    color: #B8B8B8;

    &:hover {
      text-decoration: underline;
    }
  }

  @include vp-767 {
    margin-bottom: 10px;
  }
}


.footer__socials {
  h3 {
    @include vp-767 {
      display: none;
    }
  }
}

.footer__socials-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 120px;
  margin-top: 22px;

  @include vp-767 {
    max-width: 100%;
    margin-bottom: 20px;
  }
}

.footer__socials-item {
  width: 33px;
  height: 33px;
  background: #37393F;
  backdrop-filter: blur(4.5px);
  border-radius: 11px;
  margin-right: 7px;
  margin-bottom: 9px;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    background-color: #56585E;
  }

  a {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.footer__socials-icon {
  display: block;
}

.footer__nav-bottom {
  display: flex;
  justify-content: space-between;

  @include vp-1023 {
    display: flex;
    flex-direction: column;
  }
}

.footer__copyright {
  span {
    @include font-settings ($ff-inter, 500, 14px, 1);
    color: #6B6161;
    margin-right: auto;

    @include vp-767 {
      font-size: 12px;
    }
  }

  @include vp-767 {
    order: 1;
  }
}

.footer__menu {
  display: flex;

  a {
    @include font-settings ($ff-inter, 400, 14px, 1);
    color: #6B6161;
    margin-right: 30px;

    &:hover {
      text-decoration: underline;
    }

    &:active {
      -webkit-tap-highlight-color: transparent;
    }

    @include vp-767 {
      margin: 0;
      font-size: 12px;
      margin-bottom: 5px;
    }
  }

  @include vp-767 {
    flex-direction: column;
    margin-bottom: -20px;
  }
}

a.footer__up-btn {
  display: inline-block;
  width: 24px;
  height: 24px;
  background-color: #37393F;
  color: $color-default-white;
  border: none;
  border-radius: 5px;
  padding: 5px 7px;
  cursor: pointer;

  &:hover {
    background-color: #56585E;
  }

  svg {
    display: block;
  }

  @include vp-1023 {
    align-self: flex-end;
    z-index: 10;
    margin: 0;
  }
}
