.profile {
  position: relative;
  padding: 0 20px;
}

.profile__heading {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-bottom: 20px;

  h2 {
    @include font-settings ($ff-inter, 700, 35px, 1);
    letter-spacing: 0.96px;
    text-transform: uppercase;
    margin: 5px 30px 5px 0;

    @include vp-1023 {
      margin-bottom: 14px;
    }
  }
}

.profile__link {
  @include font-settings ($ff-inter, 500, 14px, 1.5);
  position: relative;
  display: block;
  background-color: #262931;
  border-radius: 10px;
  margin: 5px 36px 5px 0;
  padding: 7px 17px 9px 35px;

  &::before {
    position: absolute;
    content: url("../img/svg/globe.svg");
    top: 10px;
    left: 12px;
    width: 14px;
    height: 14px;
  }

  &:hover {
    background: #14161B;
  }

  &:active {
    opacity: 0.6;
  }
}

.profile__rating {
  display: flex;
  align-items: center;
  padding-top: 6px;
  margin: 5px 0;

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    margin-right: 8px;
  }
}

.profile__main-contacts {
  margin: 5px 0;
  margin-left: auto;

  @include vp-767 {
    margin-left: 0;
  }
}

.profile__main-contacts-list {
  display: flex;
  flex-wrap: wrap;
}

.profile__main-contacts-item {
  width: 37px;
  height: 37px;
  background: #262931;
  border-radius: 10px;
  margin-bottom: 6px;

  a {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &:hover {
    background: #14161b;
  }
}

.profile__main-contacts-item:not(:last-child) {
  margin-right: 6px;
}

.profile__info {
  margin-bottom: 15px;

  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
    margin-bottom: 22px;

    @include vp-1023 {
      margin-bottom: 20px;
    }
  }

  h2 {
    @include font-settings ($ff-inter, 700, 18px, 1.5);
    opacity: 0.5;
  }

  h3 {
    @include font-settings ($ff-inter, 700, 16px, 1.5);
    opacity: 0.5;
  }

  h4 {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
  }

  ul,
  ol {
    margin: 0 0 21px;
    padding: 0 0 0 23px;
  }

  li {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
  }

  img {
    width: 100%;
    height: auto;
    object-fit: cover;
    margin-bottom: 10px;
  }

  a {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    font-style: normal;
    color: $color-accent;
    text-decoration: underline;
    margin-bottom: 12px;
  }

  code {
    @include font-settings ($ff-incon, 400, 14px, 1.5);
    color: $color-default-black;
    background-color: #F8F9FA;
    border-radius: 4px;
    padding: 5px 7px;
  }

  pre {
    opacity: 0.5;
    white-space: pre-wrap;
    word-wrap: break-word;
  }

  blockquote {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
    padding-left: 16px;
    border-left: 2px solid #DEE2E6;
    margin: 10px 0;
  }

  hr {
    height: 1px;
    margin: 12px 0;
    background-color: #DEE2E6;
    border: none;
  }
}

.profile__contacts-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px 23px;

  @include vp-1279 {
    margin-bottom: 29px;
  }
}

.profile__contacts-item {
  background-color: #262931;
  border-radius: 20px;
  padding: 27px 22px 22px;
  width: calc((100% / 2) - 40px);
  max-width: 356px;
  margin: 0 19.5px 30px;

  @include vp-1279 {
    min-width: 290px;
    padding: 11px 20px 23px 17px;
  }

  h3 {
    @include font-settings ($ff-inter, 600, 18px, 1.56);

    @include vp-1023 {
      font-size: 16px;
      line-height: 1.75;
    }
  }

  p {
    @include font-settings ($ff-inter, 400, 18px, 1.56);
    margin-bottom: 15px;

    @include vp-1023 {
      font-size: 16px;
      line-height: 1.75;
    }
  }
}

.profile__socials-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;

  @include vp-1279 {
    flex-direction: column;
    margin: 0;
  }
}

.profile__socials-item {
  min-width: 130px;
  min-height: 37px;
  background-color: #1E2228;
  border-radius: 10px;
  padding: 3px 5px 5px;
  margin: 0 7px 7px;
  display: flex;
  align-items: center;
  justify-content: center;

  @include vp-1023 {
    justify-content: flex-start;
    margin: 0 0 6px;
    padding: 2px 8px 3px;
  }

  a {
    @include font-settings ($ff-inter, 500, 14px, 1);
    display: flex;
    justify-content: center;
    align-items: center;
    color: $color-default-white;
  }

  svg {
    fill: $color-default-white;
    margin-right: 7px;
  }

  &:hover {
    background: #14161B;
  }

  &:active {
    opacity: 0.6;
  }
}

.profile__reviews {
  h3 {
    @include font-settings ($ff-inter, 600, 18px, 1.56);
    margin-bottom: 15px;
  }
}

.profile__reviews-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include vp-1023 {
    flex-direction: column;
  }
}

.profile__reviews-item {
  max-width: 49%;
  background-color: #262931;
  border-radius: 20px;
  padding: 23px 12px 24px 20px;
  margin-bottom: 10px;

  @include vp-1023 {
    padding: 11px 12px 11px 17px;
    max-width: 100%;
  }
}

.profile__reviews-author {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;

  h4 {
    @include font-settings ($ff-inter, 600, 18px, 1.56);
  }

  @include vp-767 {
    flex-direction: column;
    align-items: flex-start;

    h4 {
      margin-bottom: 10px;
    }
  }
}

.profile__reviews-text {
  p {
    @include font-settings ($ff-inter, 400, 14px, 1.5);
    opacity: 0.5;
  }
}
