.new {
  margin-bottom: 22px;

  @include vp-767 {
    margin-bottom: 20px;
  }
}

.new__block {
  padding: 20px;

  .tabs__content {
    display: none;
  }

  .tabs__content.active {
    display: block;
  }

  @include vp-767 {
    padding: 24px 20px;
  }
}

.new__button-wrapper {
  display: flex;
  max-width: 276px;
  min-height: 40px;
  margin-bottom: 12px;
  border-radius: 12px;
  background-color: #1A1D24;
  padding: 4px;

  @include vp-1023 {
    margin: 0 auto;
    max-width: 272px;
    justify-content: space-between;
  }
}

.new__btn {
  @include font-settings ($ff-inter, 500, 14px, 1);
  height: 32px;
  padding: 10px 8px;
  background-color: #1A1D24;
  border: none;
  display: flex;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
  cursor: pointer;

  &:first-child {
    margin-right: 5px;
  }

  &:hover {
    background-color: #14161B;
    border-radius: 8px;
  }

  &:active,
  &.active {
    background: #434549;
    box-shadow: 0px 6px 6px -6px rgba(0, 0, 0, 0.16), 0px 0px 1px rgba(0, 0, 0, 0.4);
    border-radius: 8px;
  }

  @include vp-1023 {
    padding: 8px 6px;
  }
}

.new__list {
  padding-left: 4px;

  @include vp-767 {
    padding: 5px 0 0;
  }
}

.new__item {
  display: grid;
  grid-template-columns: 587px 134px 121px;
  column-gap: 20px;
  padding-top: 18px;

  &:not(:last-child) {
    border-bottom: 1px solid #606368;
    padding-bottom: 19px;

    @include vp-1023 {
      padding: 14px 0 9px;
    }
  }

  @include vp-1023 {
    padding: 14px 0 9px;
  }

  @include vp-1023 {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat (2, auto);
    column-gap: 0;
    row-gap: 8px;
    padding-top: 9px;
  }
}

.new__item a {
  @include font-settings ($ff-inter, 600, 14px, 1);
  width: 587px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

  @include vp-1023 {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    width: 100%;
    margin-bottom: 9px;

    @supports (display: -webkit-box) {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      white-space: normal;
    }
  }
}

.new__item span {
  @include font-settings ($ff-inter, 400, 14px, 1);

  @include vp-1023 {
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    font-size: 12px;
  }
}

.new__item p {
  @include font-settings ($ff-inter, 400, 14px, 1);
  position: relative;
  padding-left: 25px;

  &::before {
    position: absolute;
    content: url("../img/svg/clock-icon.svg");
    top: 0;
    left: 0;
  }

  @include vp-1023 {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    justify-self: end;
    font-size: 12px;
  }
}
